import { Logger as GenericLogger, loggerFactory, ConsoleSink, CompositeSink } from "common";

export const logLevels = ["error", "warn", "info", "debug"] as const;
export type LogLevel = (typeof logLevels)[number];

export type Logger = GenericLogger<LogLevel>;
export const logger = loggerFactory(
  logLevels,
  new CompositeSink([
    new ConsoleSink<LogLevel>({
      error: "error",
      warn: "warn",
      info: "info",
      debug: "debug",
    }),
  ])
);
