import browser from "browser-polyfill";
import React, { useCallback, useEffect, useState } from "react";

import { createStyles, makeStyles } from "@material-ui/styles";
import { ReactComponent as IconImage } from "../assets/scanning-toast.svg";
import {
  Message,
  MessageKind,
  NotificationLevel,
  ScanProgressStatus,
  ToastSlideTransitionType,
} from "common";
import { TestId } from "../common/test-ids";
import ppTheme from "../pp-theme";

import filesize from "filesize";
import BaseToast, { levelToToastStyleProperties, SnackbarProperties } from "./base-toast";
import { Button, LinearProgress } from "@material-ui/core";

const useStyles = makeStyles(() =>
  createStyles({
    cancelButton: {
      border: `2px solid ${ppTheme.palette.secondary.main}`,
      fontSize: 12,
      fontWeight: 450,
      color: ppTheme.palette.secondary.main,
      borderWidth: 1,
      backgroundColor: "transparent",
      height: 30,
      alignSelf: "center",
      whiteSpace: "nowrap",
      marginTop: 8,
    },
    lineProgress: ({ color }: SnackbarProperties) => ({
      "&.MuiLinearProgress-colorPrimary:not(.MuiLinearProgress-buffer)": {
        backgroundColor: "#EEEEEE",
      },
      "& .MuiLinearProgress-colorPrimary": {
        backgroundColor: "#EEEEEE",
      },
      "& .MuiLinearProgress-barColorPrimary": {
        backgroundColor: color ?? ppTheme.notificationLevel.info,
      },
    }),
    linearProgressContainer: {
      marginTop: 10,
      width: "100%",
    },
  })
);

interface ScanProgressToastProperties {
  autoDismiss?: boolean;
  id: string;
  fileSizeBytes: number;
  maxScanProgressPercentage?: number;
  minScanProgressPercentage?: number;
  changeInterval: number;
  fileName: string;
  scanStatus: ScanProgressStatus;
  downloadId: number;
  notificationLevel: NotificationLevel;
  showCancelButton?: boolean;
  scanProgressStagePercentage?: number;
  slideTransitionDirection: ToastSlideTransitionType;
  shouldShowDownloadProgressInToast: boolean;
  downloadedBytes?: number;
  debuggingData?: string;
}

const maxTitleChars = 30;

function getToastTitle(fileName: string): string {
  if (fileName.length <= maxTitleChars) {
    return fileName;
  }
  const suffix = `...${fileName.split(".").slice(-1)[0]}`;
  const updatedMaxChars = Math.max(0, maxTitleChars - suffix.length);
  return `${fileName.split(".").slice(0, -1).join(".").slice(0, updatedMaxChars)}${suffix}`;
}

function getFileDownloadData(
  fileSizeBytes: number,
  scanStatus: ScanProgressStatus,
  showDownloadProgress: boolean,
  updatedDownloadedBytes?: number
): string {
  const prettyDownloadedBytes: string = filesize(updatedDownloadedBytes ?? 0, {
    base: 2,
    round: 0,
    standard: "jedec",
  });
  const prettyTotalFileSize: string = filesize(fileSizeBytes, {
    base: 2,
    standard: "jedec",
  });
  const prettyFileSize = fileSizeBytes > 0 ? prettyTotalFileSize : prettyDownloadedBytes;

  if (!showDownloadProgress) {
    return `${prettyFileSize}  \u00B7 `;
  }

  switch (scanStatus) {
    case ScanProgressStatus.Preparing:
      if (fileSizeBytes <= 0) {
        return "";
      }
      return `0 / ${prettyFileSize} \u00B7 `;
    case ScanProgressStatus.Downloading: {
      if (updatedDownloadedBytes === undefined || fileSizeBytes <= 0) {
        return `${prettyFileSize}  \u00B7 `;
      }
      if (
        prettyDownloadedBytes > prettyFileSize &&
        fileSizeBytes - updatedDownloadedBytes < 500000
      ) {
        // for cases when the round number is bigger then prettyFileSize
        return `${prettyFileSize} / ${prettyFileSize}  \u00B7 `;
      }
      return `${prettyDownloadedBytes} / ${prettyFileSize}  \u00B7 `;
    }
    default:
      return fileSizeBytes > 0
        ? `${prettyFileSize} / ${prettyFileSize} \u00B7 `
        : `${prettyFileSize} \u00B7 `;
  }
}

const ScanProgressToast: React.FC<ScanProgressToastProperties> = ({
  autoDismiss = true,
  id,
  fileSizeBytes,
  maxScanProgressPercentage,
  minScanProgressPercentage,
  scanProgressStagePercentage,
  changeInterval,
  fileName,
  scanStatus,
  downloadId,
  notificationLevel,
  showCancelButton,
  slideTransitionDirection,
  shouldShowDownloadProgressInToast,
  downloadedBytes,
  debuggingData,
}: ScanProgressToastProperties) => {
  const classes = useStyles({ color: levelToToastStyleProperties.get(notificationLevel)?.color });
  const [updatedScanProgress, setUpdatedScanProgress] = useState(
    minScanProgressPercentage ?? scanProgressStagePercentage ?? 0
  );
  const [updatedMaxScanProgress, setUpdatedMaxScanProgress] = useState(
    maxScanProgressPercentage ?? scanProgressStagePercentage
  );
  const [updatedChangeInterval, setUpdatedChangeInterval] = useState(changeInterval);
  const [updatedScanStatus, setUpdatedScanStatus] = useState(scanStatus);
  const [updatedDownloadedBytes, setUpdatedDownloadedBytes] = useState(downloadedBytes);
  const [updatedFileName, setUpdatedFileName] = useState(fileName);

  useEffect(() => {
    if (updatedScanProgress && updatedMaxScanProgress && updatedChangeInterval > 0) {
      let interval: NodeJS.Timeout | undefined = setInterval(() => {
        if (!interval) {
          return;
        }
        if (
          updatedScanProgress >= updatedMaxScanProgress - 1 &&
          updatedScanStatus !== ScanProgressStatus.Completed
        ) {
          clearInterval(interval);
          interval = undefined;
          return;
        }
        if (updatedScanStatus === ScanProgressStatus.Completed && updatedScanProgress >= 100) {
          clearInterval(interval);
          interval = undefined;
          return;
        }
        setUpdatedScanProgress(updatedScanProgress + 1);
      }, updatedChangeInterval);
      return () => {
        if (!interval) {
          return;
        }
        clearInterval(interval);
        interval = undefined;
      };
    }
  }, [updatedChangeInterval, updatedMaxScanProgress, updatedScanProgress, updatedScanStatus]);

  useEffect(() => {
    const windowEventListener = (event: MessageEvent): void => {
      contentListener(event.data);
    };
    const contentListener = (message: Message): void => {
      if (message.kind === MessageKind.RefreshScanProgressBaseDataMessage && message.id === id) {
        const { fileName } = message;
        setUpdatedFileName(fileName);
      } else if (
        message.kind === MessageKind.ShowScanProgressBarRefreshedToastMessage &&
        message.id === id
      ) {
        const {
          minScanProgressPercentage,
          maxScanProgressPercentage,
          scanProgressStagePercentage,
          scanStatus,
          changeInterval,
          downloadedBytes,
        } = message;
        const scanProgressPercentage =
          minScanProgressPercentage ?? scanProgressStagePercentage ?? 0;
        setUpdatedScanProgress(scanProgressPercentage);
        setUpdatedMaxScanProgress(maxScanProgressPercentage ?? scanProgressStagePercentage);
        setUpdatedScanStatus(scanStatus);
        setUpdatedChangeInterval(changeInterval);
        if (downloadedBytes) {
          setUpdatedDownloadedBytes(downloadedBytes);
        }
      }
    };
    window.addEventListener("message", windowEventListener);
    return (): void => {
      window.removeEventListener("message", windowEventListener);
    };
  }, [
    id,
    updatedScanProgress,
    setUpdatedScanStatus,
    setUpdatedScanProgress,
    setUpdatedMaxScanProgress,
    setUpdatedChangeInterval,
    setUpdatedDownloadedBytes,
  ]);

  const closeToast = useCallback((): void => {
    void browser.runtime.sendMessage({
      kind: MessageKind.DismissNotificationToast,
      id,
    });
  }, [id]);

  const handleCancelScan = (): void => {
    void closeToast();
    const message = {
      kind: MessageKind.CancelScanMessage,
      downloadId,
    };
    void browser.runtime.sendMessage(message);
  };

  const fileSizeData = getFileDownloadData(
    fileSizeBytes,
    updatedScanStatus,
    shouldShowDownloadProgressInToast,
    updatedDownloadedBytes
  );

  const updatedBody = (
    <>
      <span data-test-id={TestId.ProgressFileSize}>
        {fileSizeBytes < 0 && !updatedDownloadedBytes ? "" : fileSizeData}
      </span>
      <span data-test-id={TestId.ProgressScanPercentage}>{updatedScanProgress}</span>% {"\u00B7 "}
      <span data-test-id={TestId.ProgressScanStatus}>{updatedScanStatus}</span>
    </>
  );

  const buttonContent = (
    <div>
      {showCancelButton && (
        <Button className={classes.cancelButton} onClick={handleCancelScan}>
          Cancel Scan
        </Button>
      )}
    </div>
  );

  const content = (
    <div>
      <div className={classes.linearProgressContainer}>
        <LinearProgress
          variant="determinate"
          className={classes.lineProgress}
          value={updatedScanProgress}
        />
      </div>
    </div>
  );

  return (
    <BaseToast
      id={id}
      body={updatedBody}
      title={getToastTitle(updatedFileName)}
      notificationLevel={notificationLevel}
      autoDismiss={autoDismiss}
      showCloseButton={true}
      headerText={debuggingData ? `File Scan Status \u00B7 ${debuggingData}` : "File Scan Status"}
      icon={<IconImage />}
      extraButtonComponents={buttonContent}
      extraComponents={content}
      slideTransitionDirection={slideTransitionDirection}
    />
  );
};

export default ScanProgressToast;
