import { makeStyles, createStyles } from "@material-ui/core";
import React from "react";

interface BlockPageButtonsProperties {
  children?: React.ReactNode;
  isButtonsAtBottom: boolean;
}

const useStyles = makeStyles(() => {
  return createStyles({
    buttons: {
      alignItems: "center",
      display: "flex",
      flexDirection: "row",
      justifyContent: "space-between",
      paddingBottom: 20,
      paddingTop: 30,
      width: "100%",
      "& button:only-child": {
        marginLeft: "auto",
      },
    },
  });
});

export const BlockPageButtonsContainer = ({
  children,
  isButtonsAtBottom,
}: BlockPageButtonsProperties): JSX.Element => {
  const { buttons } = useStyles();
  return (
    <div style={{ order: isButtonsAtBottom ? 9999 : 0 }} className={buttons}>
      {children}
    </div>
  );
};
