import browser from "browser-polyfill";
import React, { useCallback, useEffect, useState } from "react";
import { createStyles, makeStyles, Button, ThemeProvider, Typography } from "@material-ui/core";
import clsx from "clsx";

import ppTheme from "../pp-theme";
import { ReactComponent as DisabledShieldImage } from "../assets/disabled-shield.svg";
import { ReactComponent as LoginImage } from "../assets/login.svg";
import { ReactComponent as ShieldImage } from "../assets/shield.svg";
import { ReactComponent as PpTextLogoImage } from "../assets/pp-text-logo.svg";
import { ReactComponent as ShieldButtonLogo } from "../assets/shield-button.svg";
import { ReactComponent as SettingsButtonLogo } from "../assets/settings-button.svg";
import useLocalSettings from "../hooks/use-local-settings";
import {
  MessageKind,
  ReportFalseNegativeMessageResponse,
  GetCurrentSiteMessageResponse,
  ActivationStatus,
  GetExtensionSettingsMessageResponse,
  BrowserName,
  browserName,
} from "common";
import ReportWebsitePopup, {
  reportWebsiteResultMap,
  ReportWebsiteState,
} from "./report-website-popup";
import useHmcConfiguration from "../hooks/use-hmc-configuration";
import { TestId } from "../common/test-ids";
import { signinUrl } from "../common/url";
import SettingsPopup from "./settings-popup";
import useLastUser from "../hooks/use-last-user";
import TabProtectionPopup from "./tab-protection-popup";
import { ArrowDownward } from "@material-ui/icons";

const useStyles = makeStyles(() =>
  createStyles({
    app: {
      textAlign: "left",
      display: "flex",
      fontFamily: "Circular Pro",
      flexDirection: "column",
      backgroundColor: "#FFFFFF",
      margin: "20px 16px 32px",
      width: 330,
    },
    content: {
      display: "flex",
      flexDirection: "column",
      margin: "0 auto",
    },
    contentRow: {
      alignItems: "center",
      display: "flex",
      flexDirection: "row",
      justifyContent: "center",
    },
    contentTitle: {
      textAlign: "center",
      fontSize: 24,
      lineHeight: "38px",
      fontWeight: 900,
      color: "#FF5400",
      marginTop: 12,
      letterSpacing: -1.5,
    },
    contentSubtitle: {
      textAlign: "center",
      fontSize: 16,
      lineHeight: "22px",
      color: "#000033",
      marginTop: 6,
      width: 242,
    },
    contentAction: {
      marginTop: 16,
    },
    disabledContentTitle: {
      color: "#AAAAAA",
    },
    doneMessage: {
      textAlign: "center",
      fontSize: 16,
      lineHeight: "22px",
      color: "#FF5400",
      marginTop: 10,
      width: 242,
    },
    downloadManagerButton: {
      "&:hover": {
        backgroundColor: "transparent",
      },
      backgroundColor: "transparent",
      bottom: 7,
      color: "black",
      height: 36,
      minWidth: 34,
      padding: 0,
    },
    actionButton: {
      width: 214,
      height: 39,
    },
    settingsButton: {
      backgroundColor: "transparent",
      padding: 0,
      minHeight: 20,
      minWidth: 20,
      "&:hover": {
        backgroundColor: "transparent",
      },
    },
    logoRow: {
      display: "flex",
      flexDirection: "row",
      justifyContent: "space-between",
      alignItems: "flex-start",
    },
    popupWrapper: {
      display: "inline-block",
      backgroundColor: "#FFFFFF",
    },
  })
);

const notToReportUrlPrefixes = [
  "chrome://",
  browser.runtime.getURL(""),
  "favorites://",
  "history://",
];

enum PopupPage {
  Main = "main",
  Settings = "settings",
  TabProtection = "tabProtection",
}

const StandalonePopup = (): JSX.Element => {
  const classes = useStyles();
  const localSettings = useLocalSettings();
  const configuration = useHmcConfiguration();

  const [allowReportingWebsiteAsMalicious, setAllowReportingWebsiteAsMalicious] =
    useState<boolean>(false);
  const [showReportButton, setShowReportButton] = useState<boolean>(true);
  const [showTabProtectionButton, setShowTabProtectionButton] = useState<boolean>(false);
  const [quickWebsiteReporting, setQuickWebsiteReporting] = useState<boolean>(false);
  const [isReportWebsiteMaliciousPage, setIsReportWebsiteMaliciousPage] = useState<boolean>(false);
  const [reportWebsiteState, setReportWebsiteState] = useState<ReportWebsiteState>(
    ReportWebsiteState.NEW
  );
  const [currentPage, setCurrentPage] = useState<PopupPage>(PopupPage.Main);
  const [showSessionDebugging, setShowSessionDebugging] = useState<boolean>(false);
  const [debugSessionInProgress, setDebugSessionInProgress] = useState<boolean>(false);
  const [debugSessionCounter, setDebugSessionCounter] = useState<number>(0);
  const [lastUser, setLastUser] = useState<string | undefined>(useLastUser());
  const [showInternalDownloadsManager, setShowInternalDownloadsManager] = useState<boolean>(false);

  useEffect(() => {
    void (async () => {
      if (configuration.activationStatus === ActivationStatus.ActiveForEmail) {
        setShowReportButton(false);
        return;
      }
      const result = await browser.tabs.query({ active: true, currentWindow: true });
      const tabId = result[0].id ?? -1;
      const { url } = (await browser.runtime.sendMessage({
        kind: MessageKind.GetCurrentSite,
        tabId,
      })) as GetCurrentSiteMessageResponse;
      setShowReportButton(!notToReportUrlPrefixes.some(prefix => url.startsWith(prefix)));
    })();
  }, [configuration.activationStatus]);

  useEffect(() => {
    if (configuration) {
      setAllowReportingWebsiteAsMalicious(configuration.allowReportingWebsiteAsMalicious ?? false);
      setQuickWebsiteReporting(configuration?.quickWebsiteReporting ?? false);
      setShowTabProtectionButton(Boolean(configuration?.enableTabProtectionPopup));
      if (configuration.userDisplayName) {
        setLastUser(configuration.userDisplayName);
      }
      setShowInternalDownloadsManager(Boolean(configuration.showInternalDownloadManager));
    }
  }, [
    configuration,
    setQuickWebsiteReporting,
    setAllowReportingWebsiteAsMalicious,
    setShowTabProtectionButton,
    setLastUser,
    setShowInternalDownloadsManager,
  ]);

  const incrementDebugSessionCounter = useCallback(async (): Promise<void> => {
    const { storedDebugSessionCounter } = await browser.storage.local.get(
      "storedDebugSessionCounter"
    );
    if (storedDebugSessionCounter) {
      const nextCount = Number(storedDebugSessionCounter) + 1;
      await browser.storage.local.set({
        storedDebugSessionCounter: nextCount,
      });
      setDebugSessionCounter(nextCount);
    }
  }, []);

  const toggleDebugSessionInProgress = useCallback(async (): Promise<void> => {
    const value = !debugSessionInProgress;
    setDebugSessionInProgress(value);
    await browser.storage.local.set({
      storedDebugSessionInProgress: value,
    });
  }, [debugSessionInProgress]);

  useEffect(() => {
    void browser.storage.local
      .get(["storedDebugSessionCounter", "storedDebugSessionInProgress"])
      .then(({ storedDebugSessionCounter, storedDebugSessionInProgress }) => {
        if (Number.isSafeInteger(storedDebugSessionCounter)) {
          setDebugSessionCounter(Number(storedDebugSessionCounter));
        } else {
          void browser.storage.local.set({ storedDebugSessionCounter: 1 });
          setDebugSessionCounter(1);
        }

        if (storedDebugSessionInProgress !== undefined) {
          setDebugSessionInProgress(Boolean(storedDebugSessionInProgress));
        } else {
          void browser.storage.local.set({ storedDebugSessionInProgress: false });
          setDebugSessionInProgress(false);
        }
      });
  }, [incrementDebugSessionCounter, toggleDebugSessionInProgress]);

  useEffect(() => {
    void browser.runtime
      .sendMessage({
        kind: MessageKind.GetExtensionSettings,
      })
      .then(({ extensionSettings }: GetExtensionSettingsMessageResponse) => {
        setShowSessionDebugging(
          (extensionSettings.enableSessionDebugging && configuration?.enableSessionDebugging) ??
            false
        );
      });
  }, [configuration]);

  const onSignin = useCallback(() => {
    void browser.tabs.create({
      url: localSettings?.idpSigninUrl || signinUrl,
    });
  }, [localSettings]);

  const onReportMaliciousWebsiteClick = useCallback((): void => {
    if (quickWebsiteReporting) {
      void browser.runtime
        .sendMessage({ kind: MessageKind.ReportFalseNegative })
        .then((response: ReportFalseNegativeMessageResponse) => {
          setReportWebsiteState(
            response.success ?? false ? ReportWebsiteState.SUCCESS : ReportWebsiteState.FAILED
          );
        });
    } else {
      setIsReportWebsiteMaliciousPage(true);
    }
    setReportWebsiteState(ReportWebsiteState.SENT);
  }, [quickWebsiteReporting]);

  const onDebuggingSessionClicked = useCallback((): void => {
    void browser.runtime.sendMessage({
      kind: MessageKind.WriteLog,
      log: `========= Debugging Session #${debugSessionCounter} ${
        debugSessionInProgress ? "Ended" : "Started"
      } =========`,
      level: "debug",
      optionalParameters: [],
    });
    if (debugSessionInProgress) {
      void incrementDebugSessionCounter();
    }
    void toggleDebugSessionInProgress();
  }, [
    debugSessionCounter,
    debugSessionInProgress,
    incrementDebugSessionCounter,
    toggleDebugSessionInProgress,
  ]);

  const onDownloadManagerClicked = useCallback((): void => {
    void browser.runtime.sendMessage({
      kind: MessageKind.OpenDownloadManager,
    });
  }, []);

  const pageToContent = {
    [PopupPage.Main]: (
      <div className={classes.content}>
        {localSettings?.lastUser ? (
          <>
            {configuration.activationStatus !== ActivationStatus.Disabled ? (
              <>
                <div className={classes.contentRow}>
                  <ShieldImage />
                </div>
                <div className={classes.contentRow}>
                  <Typography
                    component="div"
                    className={classes.contentTitle}
                    data-testid={TestId.BrowserProtectionIsActive}
                  >
                    Browser Protection is Active
                  </Typography>
                </div>
                <div className={classes.contentRow}>
                  <Typography component="div" className={classes.contentSubtitle}>
                    Your browser is protected against advanced web threats
                  </Typography>
                </div>
                {showSessionDebugging && (
                  <div className={classes.contentRow}>
                    <div className={classes.contentAction}>
                      <Button
                        variant="contained"
                        className={classes.actionButton}
                        onClick={onDebuggingSessionClicked}
                        color="primary"
                        disableElevation
                        data-testid={TestId.StartSessionButtonExtensionPopup}
                      >
                        {`${
                          debugSessionInProgress ? "End" : "Start"
                        } session #${debugSessionCounter}`}
                      </Button>
                    </div>
                  </div>
                )}
                {!isReportWebsiteMaliciousPage ? (
                  <>
                    {reportWebsiteState === ReportWebsiteState.SUCCESS ||
                    reportWebsiteState === ReportWebsiteState.FAILED ? (
                      <div className={classes.contentRow}>
                        <Typography component="div" className={classes.doneMessage}>
                          {reportWebsiteResultMap.get(reportWebsiteState)}
                        </Typography>
                      </div>
                    ) : (
                      <>
                        {allowReportingWebsiteAsMalicious && showReportButton && (
                          <div className={classes.contentRow}>
                            <div className={classes.contentAction}>
                              <Button
                                variant="contained"
                                className={classes.actionButton}
                                onClick={onReportMaliciousWebsiteClick}
                                color="primary"
                                disableElevation
                                disabled={reportWebsiteState === ReportWebsiteState.SENT}
                              >
                                Report malicious
                              </Button>
                            </div>
                          </div>
                        )}
                      </>
                    )}
                  </>
                ) : (
                  <ReportWebsitePopup />
                )}
              </>
            ) : (
              <>
                <div className={classes.contentRow}>
                  <DisabledShieldImage />
                </div>
                <div className={classes.contentRow}>
                  <Typography
                    component="div"
                    className={clsx(classes.contentTitle, classes.disabledContentTitle)}
                  >
                    Browser Protection is Inactive
                  </Typography>
                </div>
                <div className={classes.contentRow}>
                  <Typography component="div" className={classes.contentSubtitle}>
                    Subscription deactivated.
                  </Typography>
                </div>
              </>
            )}
          </>
        ) : (
          <>
            <div className={classes.contentRow}>
              <div>
                <LoginImage />
              </div>
            </div>
            <div className={classes.contentRow}>
              <Typography component="div" className={classes.contentTitle}>
                Log in to protect your browser
              </Typography>
            </div>
            <div className={classes.contentRow}>
              <Typography component="div" className={classes.contentSubtitle}>
                Please login to activate your browser protection
              </Typography>
            </div>
            <div className={classes.contentRow}>
              <div className={classes.contentAction}>
                <Button
                  data-testid={TestId.SigninButton}
                  variant="contained"
                  className={classes.actionButton}
                  color="primary"
                  onClick={onSignin}
                  disableElevation
                >
                  Login
                </Button>
              </div>
            </div>
          </>
        )}
      </div>
    ),
    [PopupPage.Settings]: <SettingsPopup lastUser={lastUser} />,
    [PopupPage.TabProtection]: <TabProtectionPopup />,
  };

  return (
    <ThemeProvider theme={ppTheme}>
      <div className={classes.popupWrapper}>
        <div className={classes.app}>
          <div className={classes.logoRow}>
            <PpTextLogoImage />
            {currentPage === PopupPage.Main && showTabProtectionButton && (
              <Button
                style={{ marginLeft: "auto", paddingRight: 8, top: "-2px" }}
                className={classes.settingsButton}
                onClick={() => setCurrentPage(PopupPage.TabProtection)}
              >
                <ShieldButtonLogo data-testid={TestId.ShieldButton} />
              </Button>
            )}
            {currentPage === PopupPage.Main && (
              <Button
                className={classes.settingsButton}
                onClick={() => setCurrentPage(PopupPage.Settings)}
              >
                <SettingsButtonLogo data-testid={TestId.SettingsButton} />
              </Button>
            )}
            {currentPage === PopupPage.Main &&
              showInternalDownloadsManager &&
              BrowserName.Safari !== browserName && (
                <Button
                  variant="contained"
                  className={classes.downloadManagerButton}
                  onClick={onDownloadManagerClicked}
                  color="primary"
                  disableElevation
                >
                  <ArrowDownward />
                </Button>
              )}
          </div>
          {pageToContent[currentPage]}
        </div>
      </div>
    </ThemeProvider>
  );
};

export default StandalonePopup;
