import React, { useEffect, useState } from "react";
import { createStyles, Link, makeStyles } from "@material-ui/core";
import { ReactComponent as WebsiteBlockLogo } from "../assets/website-block.svg";
import { getQueryParameters, getUrl } from "../utils/url";
import useHmcConfiguration from "../hooks/use-hmc-configuration";
import { BlockPage, BlockPageColor, BlockType, ContinueAnywayType } from "./block-page";
import { CustomMaliciousWebsiteMessage } from "../hooks/custome-message";
import { TestId } from "../common/test-ids";
import { BlockPageDetectionMode } from "common";

const useStyles = makeStyles(() =>
  createStyles({
    learnMoreLink: {
      color: "#FFFFFF",
      fontWeight: 600,
      textDecoration: "underline",
    },
    url: {
      fontWeight: 900,
      wordBreak: "break-all",
    },
  })
);

export const WebsiteBlock = (): JSX.Element => {
  const classes = useStyles();
  const configuration = useHmcConfiguration();
  const [url, setUrl] = useState<string>();
  const [message, setMessage] = useState<string>();
  const [detectionMode, setDetectionMode] = useState<BlockPageDetectionMode>(
    BlockPageDetectionMode.Block
  );

  useEffect(() => {
    setUrl(getUrl());
    const { blockPageDetectionMode } = getQueryParameters();
    setDetectionMode(blockPageDetectionMode as BlockPageDetectionMode);
  }, [setUrl, setDetectionMode]);

  useEffect(() => {
    if (configuration) {
      setMessage(configuration.maliciousWebsiteMessage);
    }
  }, [configuration]);

  return (
    <BlockPage
      allowBackToSafety
      continueAnyway={ContinueAnywayType.Link}
      detectionMode={detectionMode}
      allowReportingWebsite
      icon={<WebsiteBlockLogo />}
      title="Deceptive site ahead"
      color={BlockPageColor.Red}
      subtitle={
        <>
          {message ? (
            <CustomMaliciousWebsiteMessage message={message} url={url} />
          ) : (
            <>
              Attackers on{" "}
              <span className={classes.url} data-testid={TestId.WebsiteBlockUrl}>
                {url}
              </span>{" "}
              may trick you into doing something dangerous like installing software or revealing
              your personal information (for example, passwords, phone numbers, or credit
              cards).&nbsp;
              <Link
                className={classes.learnMoreLink}
                href="https://docs.perception-point.io/user/Content/Endpoint/Unsafe-downloads.htm?cshid=5000"
                target="_blank"
                data-testid={TestId.LearnMoreLink}
              >
                Learn more
              </Link>
            </>
          )}
        </>
      }
      blockType={BlockType.MaliciousWebsite}
      details={
        <>
          The <span className={classes.url}>{url}</span> site may:
          <ul>
            <li>
              Install dangerous programs that may harm your computer or negatively affect your
              browsing experience.
            </li>
            <li>
              Trick you into revealing some of your personal information, such as passwords, phone
              numbers, and credit card details.
            </li>
            <li>
              Your company&apos;s browsing policies may also prohibit you from accessing the site.
            </li>
          </ul>
        </>
      }
      testid={TestId.WebsiteBlockPage}
    />
  );
};
