import React, { useEffect, useState } from "react";
import { createStyles, Link, makeStyles } from "@material-ui/core";
import { ReactComponent as BlockPageBackground } from "../assets/block-page-background.svg";
import { ReactComponent as FileWarningLogo } from "../assets/file-warning.svg";
import { TestId } from "../common/test-ids";
import { getQueryParameters } from "../utils/url";
import useHmcConfiguration from "../hooks/use-hmc-configuration";
import { PpTextLogo } from "../hooks/pp-text-logo";
import { CustomMaliciousDownloadMessage } from "../hooks/custome-message";
import {
  CustomLink,
  getRelevantCustomLinkConfigurationBasedOnBlockType,
} from "../components/custom-link";
import { BlockType } from "./block-page";
import { BlockPageDetectionMode } from "common";

const useStyles = makeStyles(() =>
  createStyles({
    content: {
      alignItems: "center",
      display: "flex",
      flexDirection: "row",
      fontFamily: "Circular Pro, Arial",
      height: "100vh",
    },
    decorations: {
      height: "70%",
      marginTop: "auto",
    },
    description: {
      backgroundColor: "#A20000",
      color: "#FFFFFF",
      display: "flex",
      flexDirection: "column",
      fontSize: 20,
      justifyContent: "center",
      height: "inherit",
      width: "80%",
    },
    descriptionWrapper: {
      maxWidth: 800,
      margin: "0% 14%",
    },
    divider: {
      border: "none",
      borderTop: "2px solid #000033",
      display: "inline-flex",
      marginBottom: 20,
      marginTop: 20,
      width: 120,
    },
    fileName: {
      wordBreak: "break-all",
    },
    learnMoreLink: {
      color: "#FFFFFF",
      fontWeight: 600,
      textDecoration: "underline",
    },
    left: {
      alignItems: "start",
      display: "flex",
      height: "inherit",
      flexDirection: "column",
      justifyContent: "space-between",
      padding: "0px 60px",
      width: "20%",
    },
    pplogo: {
      marginTop: 40,
    },
    title: {
      fontSize: 42,
      fontWeight: 900,
      lineHeight: "46px",
    },
    subtitle: {
      fontSize: 18,
    },
  })
);

interface Content {
  title: string;
  subtitle: JSX.Element;
}

export const FileWarning = (): JSX.Element => {
  const classes = useStyles();
  const configuration = useHmcConfiguration();
  const [fileName, setFileName] = useState<string>();
  const [message, setMessage] = useState<string>();
  const [customLink, setCustomLink] = useState<string | undefined>();
  const [blockPageDetectionMode, setBlockPageDetectionMode] = useState<BlockPageDetectionMode>(
    BlockPageDetectionMode.Block
  );

  useEffect(() => {
    const { fileName, blockPageDetectionMode } = getQueryParameters();
    setBlockPageDetectionMode(
      (blockPageDetectionMode as BlockPageDetectionMode) ?? BlockPageDetectionMode.Block
    );
    setFileName(fileName as string);
  }, [setFileName]);

  useEffect(() => {
    if (configuration) {
      setMessage(configuration.maliciousDownloadMessage);
      const customLink = getRelevantCustomLinkConfigurationBasedOnBlockType(
        configuration,
        BlockType.File
      );
      if (customLink) {
        setCustomLink(customLink);
      }
    }
  }, [configuration]);

  const contentByDetectionMode: Record<BlockPageDetectionMode, Content> = {
    [BlockPageDetectionMode.Warn]: {
      title: "Do not launch the downloaded file",
      subtitle: (
        <>
          <span className={classes.fileName}>{fileName}</span>
          &nbsp; is dangerous - launching it may let attackers steal information or cause other
          harm. Do not click or launch this downloaded file to avoid damaging your device, your
          data, and your organization. &nbsp;
        </>
      ),
    },
    [BlockPageDetectionMode.Block]: {
      title: "Malicious file download was blocked",
      subtitle: (
        <>
          <span className={classes.fileName}>{fileName}</span>
          &nbsp; is a malicious file that may harm your computer. Its download had been blocked by
          policy. &nbsp;
        </>
      ),
    },
  };

  const content = contentByDetectionMode[blockPageDetectionMode];

  return (
    <div className={classes.content} data-testid={TestId.FileWarning}>
      <div className={classes.left}>
        <PpTextLogo className={classes.pplogo} />
        <BlockPageBackground className={classes.decorations} />
      </div>
      <div className={classes.description}>
        <div className={classes.descriptionWrapper}>
          <FileWarningLogo />
          <div className={classes.title} data-testid={TestId.FileWarningTitle}>
            {content.title}
          </div>
          <hr className={classes.divider} />
          {message ? (
            <CustomMaliciousDownloadMessage message={message} filename={fileName} />
          ) : (
            <div className={classes.subtitle} data-testid={TestId.FileWarningSubTitle}>
              {content.subtitle}
              <Link
                className={classes.learnMoreLink}
                href="https://docs.perception-point.io/user/Content/Endpoint/Unsafe-downloads.htm?cshid=5000"
                target="_blank"
                data-testid={TestId.LearnMoreLink}
              >
                Learn More
              </Link>
            </div>
          )}
          {customLink && <CustomLink blockType={BlockType.File} customLink={customLink} />}
        </div>
      </div>
    </div>
  );
};
