import browser from "browser-polyfill";
import React, { useCallback, useState } from "react";
import { createStyles, makeStyles, TextField } from "@material-ui/core";
import { ReactComponent as WebsiteBlockLogo } from "../assets/website-block.svg";
import { ReactComponent as ErrorLogo } from "../assets/error-logo.svg";
import { BlockPageColor, BlockType } from "./block-page";
import { useSubmitReport } from "../hooks/block-page-hooks";
import { BlockPageButtons } from "../components/block-page-buttons";
import { BlockPageContainer } from "../components/block-page-container";
import { BlockPageDescription } from "../components/block-page-description";
import { BlockPageHeader } from "../components/block-page-header";
import { TestId } from "../common/test-ids";
import { MessageKind } from "common";

const useStyles = makeStyles(() =>
  createStyles({
    contents: {
      display: "flex",
      flexDirection: "column",
    },
    errorText: {
      fontSize: 12,
      fontWeight: 600,
      padding: "10px 0px",
    },
    reportTitle: {
      fontSize: 14,
      fontWeight: 600,
      marginBottom: 8,
      marginTop: 30,
      textTransform: "uppercase",
    },
    textfield: {
      "& .MuiInputBase-input": {
        color: ({ secondary }: StyleParameters) => secondary,
      },
      "& .MuiInputLabel-root": {
        color: ({ secondary }: StyleParameters) => secondary,
      },
      "& .MuiOutlinedInput-root": {
        borderRadius: 0,
        "& fieldset": {
          borderColor: ({ secondary }: StyleParameters) => secondary,
          borderWidth: 2,
        },
        "&:hover fieldset": {
          borderColor: ({ secondary }: StyleParameters) => secondary,
          borderWidth: 2,
        },
        "&.Mui-focused fieldset": {
          borderColor: ({ secondary }: StyleParameters) => secondary,
          borderWidth: 2,
        },
        "&.Mui-disabled fieldset": {
          borderColor: ({ secondary }: StyleParameters) => secondary,
          borderWidth: 2,
        },
      },
    },
  })
);

interface StyleParameters {
  primary: BlockPageColor;
  secondary: BlockPageColor;
}

interface ReportWebsiteCleanProperties {
  goBack: () => void;
  scanId: string;
  url: string;
  styleParameters: StyleParameters;
}

export const ReportWebsiteClean = ({
  goBack,
  scanId,
  url,
  styleParameters,
}: ReportWebsiteCleanProperties): JSX.Element => {
  const classes = useStyles(styleParameters);
  const [comment, setComment] = useState<string>("");
  const [isCommentValid, setIsCommentValid] = useState<boolean>(true);
  const [isSuccess, setIsSuccess] = useState<boolean>(false);
  const [isDone, setIsDone] = useState<boolean>(false);
  const [closeTabCalled, setCloseTabCalled] = useState<boolean>(false);

  const onCommentChange = (event: React.ChangeEvent<HTMLInputElement>): void => {
    setIsCommentValid(/^[\d\s(),.A-Za-z-]*$/.test(event.target.value));
    setComment(event.target.value);
  };

  const onCloseTabClick = useCallback((): void => {
    if (closeTabCalled) {
      return;
    }
    void browser.runtime.sendMessage({ kind: MessageKind.CloseTab });
    setCloseTabCalled(true);
  }, [closeTabCalled]);

  const submitReport = useSubmitReport(comment, scanId, isCommentValid, setIsDone, setIsSuccess);

  const reportForm = (
    <BlockPageContainer
      styleParameters={styleParameters}
      testid={TestId.ReportDetectionIssueReportPage}
    >
      <BlockPageHeader icon={<WebsiteBlockLogo />} title="Report a detection issue" />
      <BlockPageDescription
        subtitle={
          <div className={classes.contents}>
            <div>
              If you believe this is a safe website to browse, please complete the form below to
              report the error.
            </div>
            <span className={classes.reportTitle}>URL:</span>
            <TextField
              disabled
              className={classes.textfield}
              variant="outlined"
              value={url}
              inputProps={{
                "data-testid": TestId.ReportDetectionIssueUrlInput,
              }}
            />
            <span className={classes.reportTitle}>Additional comment (optional):</span>
            <TextField
              multiline
              rows={4}
              onChange={onCommentChange}
              className={classes.textfield}
              variant="outlined"
              inputProps={{
                "data-testid": TestId.ReportDetectionIssueCommentInput,
                maxLength: 150,
              }}
            />
            <div
              className={classes.errorText}
              style={{ visibility: isCommentValid ? "hidden" : "visible" }}
            >
              Invalid characters in comment.
            </div>
          </div>
        }
        blockType={BlockType.MaliciousWebsite}
        displayCustomLink={false}
      />
      <BlockPageButtons
        styleParameters={{ ...styleParameters, isButtonsAtBottom: true }}
        buttons={[
          { label: "Back", onClick: goBack, testid: TestId.ReportDetectionIssueBackButton },
          {
            label: "Submit report",
            onClick: submitReport,
            testid: TestId.ReportDetectionIssueSubmitButton,
          },
        ]}
      />
    </BlockPageContainer>
  );

  const successPage = (
    <BlockPageContainer
      styleParameters={styleParameters}
      testid={TestId.ReportDetectionIssueSuccessPage}
    >
      <BlockPageHeader icon={<WebsiteBlockLogo />} title="Report a detection issue" />
      <BlockPageDescription
        subtitle={
          <>
            Thank you for submitting the detection issue!
            <br />
            <br />
            Your report has been submitted and is being reviewed.
          </>
        }
        blockType={BlockType.MaliciousWebsite}
        displayCustomLink={false}
      />
      <BlockPageButtons
        styleParameters={{ ...styleParameters, isButtonsAtBottom: true }}
        buttons={[
          {
            label: "Close tab",
            onClick: onCloseTabClick,
            inverted: true,
            testid: TestId.ReportDetectionIssueCloseTabButton,
          },
        ]}
      />
    </BlockPageContainer>
  );

  const failurePage = (
    <BlockPageContainer
      styleParameters={styleParameters}
      testid={TestId.ReportDetectionIssueFailurePage}
    >
      <BlockPageHeader icon={<ErrorLogo />} title="Oh no!" />
      <BlockPageDescription
        subtitle={
          <>
            Something went wrong while submitting the detection issue.
            <br />
            <br />
            Please reload the page or try again later.
          </>
        }
        blockType={BlockType.MaliciousWebsite}
        displayCustomLink={false}
      />
      <BlockPageButtons
        styleParameters={{ ...styleParameters, isButtonsAtBottom: true }}
        buttons={[
          {
            label: "Reload",
            onClick: goBack,
            inverted: true,
            testid: TestId.ReportDetectionIssueReloadButton,
          },
        ]}
      />
    </BlockPageContainer>
  );

  return (
    <>
      {!isDone && reportForm}
      {isDone && isSuccess && successPage}
      {isDone && !isSuccess && failurePage}
    </>
  );
};
