import { createStyles, makeStyles } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { ReactComponent as BlockedExtensionLogo } from "../assets/blocked-extension-icon.svg";
import { BlockPage, BlockPageColor, BlockType } from "./block-page";
import { getQueryParameters } from "../utils/url";
import { TestId } from "../common/test-ids";

const useStyles = makeStyles(() =>
  createStyles({
    fileName: {
      wordBreak: "break-all",
    },
    subtitle: {
      fontWeight: 600,
      marginBottom: 20,
    },
  })
);

export const DisabledExtensionBlockPage = (): JSX.Element => {
  const classes = useStyles();
  const [extensionDisplayName, setExtensionDisplayName] = useState<string>();
  const [isSuccessfulDisabling, setIsSuccessfulDisabling] = useState<boolean>();

  useEffect(() => {
    const { extensionDisplayName, isSuccessfulDisabling } = getQueryParameters();
    setExtensionDisplayName(extensionDisplayName as string);
    setIsSuccessfulDisabling(isSuccessfulDisabling === "true");
  }, []);

  const title = isSuccessfulDisabling
    ? `"${extensionDisplayName as string}" extension was disabled`
    : `"${extensionDisplayName as string}" extension is not permitted`;

  const subtitle = isSuccessfulDisabling ? (
    <>
      {"Usage of the "}
      <span className={classes.fileName} data-testid={TestId.DisabledExtensionDisplayName}>
        {`"${extensionDisplayName as string}"`}
      </span>
      {
        " extension is not allowed by your administrator due to the company's browser extension policies."
      }
    </>
  ) : (
    <>
      {"Usage of the "}
      <span className={classes.fileName} data-testid={TestId.DisabledExtensionDisplayName}>
        {`"${extensionDisplayName as string}"`}
      </span>
      {
        " extension is not permitted by the company's browser extension policies. Please uninstall the extension."
      }
    </>
  );

  return (
    <BlockPage
      icon={<BlockedExtensionLogo />}
      title={title}
      color={BlockPageColor.Gray}
      subtitle={
        <div className={classes.subtitle} data-testid={TestId.DisabledExtensionBlockPageSubtitle}>
          {subtitle}
        </div>
      }
      blockType={BlockType.Extension}
      testid={TestId.DisabledExtensionBlockPage}
    />
  );
};
