import React, { useEffect, useState } from "react";
import { PageType } from "common";
import {
  useBackToSafety,
  useContinueAnyway,
  useSubmitPassword,
  useSubmitReport,
} from "../hooks/block-page-hooks";
import useCustomHtmlPage from "../hooks/use-custom-html-page";
import { getQueryParameters } from "../utils/url";
import { isArray } from "lodash";
import useHmcConfiguration from "../hooks/use-hmc-configuration";

export interface CustomHtmlPageProperties {
  pageType: PageType;
  originalPage?: JSX.Element;
}

enum EventType {
  Click = "click",
  Submit = "submit",
  Input = "input",
}

interface EventListenerDetails {
  element: Element;
  callback: (event?: Event | InputEvent) => void;
  eventType: EventType;
}

const addEventListenerById = (
  id: string,
  callback: (event?: Event | InputEvent) => void,
  eventType: EventType
): EventListenerDetails | undefined => {
  const element = document.querySelector(`#${id}`);
  if (element) {
    const withPreventDefault = (event?: Event | InputEvent): void => {
      event?.preventDefault();
      callback(event);
    };
    element.addEventListener(eventType, withPreventDefault);
    return { element, callback: withPreventDefault, eventType };
  }
};

const removeEventListener = (eventListenerDetails: EventListenerDetails): void => {
  eventListenerDetails.element.removeEventListener(
    eventListenerDetails.eventType,
    eventListenerDetails.callback
  );
};

const setElementValue = (id: CustomElementId, value?: string | string[]): void => {
  if ((value && !Array.isArray(value)) || value === undefined) {
    const elements = document.querySelectorAll(`#${id}`);
    if (elements.length > 0) {
      for (const element of elements) {
        element.textContent = value === undefined ? "" : value;
      }
    }
  }
};

enum CustomElementId {
  Index = "abs-index",
  ContinueAnywaySection = "abs-continue-anyway-section",
  ContinueAnyway = "abs-continue-anyway",
  BackToSafety = "abs-back-to-safety",
  Details = "abs-details",
  ToggleDetails = "abs-toggle-details",
  Logo = "abs-logo-click",
  ClosePage = "abs-close-page",
  ReloadPage = "abs-reload-page",
  Report = "abs-report",
  ShowReport = "abs-show-report",
  CloseReport = "abs-close-report",
  ReportForm = "abs-report-form",
  ReportComment = "abs-report-comment",
  ReportSuccess = "abs-report-success",
  ReportFailure = "abs-report-failure",
  InvalidReportComment = "abs-invalid-comment",
  WebsiteUrl = "abs-website-url",
  WebsiteDomain = "abs-website-domain",
  Filename = "abs-file-name",
  UnscannableReason = "abs-unscannable-reason",
  PasswordForm = "abs-password-form",
  PasswordFormPassword = "abs-password-form-password",
  LearnMore = "abs-learn-more",
  Category = "abs-category",
  TPEName = "abs-tpe-name",
  TPEDisabledReason = "abs-tpe-disabled-reason",
  TPEDisabledNextAction = "abs-tpe-next-action",
  TPERiskScore = "abs-tpe-risk-score",
  BlockFeatures = "abs-block-features",
  WarnFeatures = "abs-warn-features",
}

const styles = `
        body { 
          overscroll-behavior: none; 
        }

        .abs-hidden {
          display: none !important;
        }

        .abs-clear-input-style {
          display: inline !important;
          cursor: text !important;
        }

        .abs-clear-input-style:focus {
            outline: none !important;
        }
       `;

const appendStyle = (html: string): string => {
  const styleIndex = html.indexOf("<style>");
  if (styleIndex !== -1) {
    const styleEndIndex = html.indexOf("</style>", styleIndex) + "</style>".length;
    const existingStyles = html.slice(styleIndex, styleEndIndex);
    const updatedStyles = existingStyles.replace("</style>", `${styles}</style>`);
    html = html.slice(0, styleIndex) + updatedStyles + html.slice(styleEndIndex);
    return html;
  }

  const headIndex = html.indexOf("<head>");
  if (headIndex !== -1) {
    const headEndIndex = headIndex + "<head>".length;
    html =
      html.slice(0, headEndIndex) +
      `<style>
              ${styles}
            </style>
            ` +
      html.slice(headEndIndex);
  }
  return html;
};

const getElement = (id: CustomElementId): Element | undefined => {
  return document.querySelector(`#${id}`) || undefined;
};

const toggleElementHidden = (id: CustomElementId): void => {
  const element = getElement(id);
  if (!element) {
    return;
  }
  element.classList.toggle("abs-hidden");
};

const useCustomReportPage = (currentPage: string | undefined): void => {
  const [scanId, setScanId] = useState<string>("");
  const [comment, setComment] = useState<string>("");
  const [isCommentValid, setIsCommentValid] = useState<boolean>(true);
  const [isSuccess, setIsSuccess] = useState<boolean>(false);
  const [isDone, setIsDone] = useState<boolean>(false);

  const submitReport = useSubmitReport(comment, scanId, isCommentValid, setIsDone, setIsSuccess);

  const onCommentChange = (event?: Event | InputEvent): void => {
    const target = event?.target as HTMLInputElement;
    const isValid = /^[\d\s(),.A-Za-z-]*$/.test(target.value);
    setIsCommentValid(isValid);
    if (isValid) {
      setComment(target.value);
    }
  };

  useEffect(() => {
    const { scanId } = getQueryParameters();
    setScanId(scanId as string);
  }, [setScanId, comment, scanId]);

  useEffect(() => {
    const addEventListeners = (): (EventListenerDetails | undefined)[] => {
      return [
        addEventListenerById(CustomElementId.ReportComment, onCommentChange, EventType.Input),
        addEventListenerById(CustomElementId.ReportForm, submitReport, EventType.Submit),
      ];
    };
    const eventsListenersDetails = addEventListeners();

    return () => {
      for (const eventListenerDetails of eventsListenersDetails) {
        if (eventListenerDetails) {
          removeEventListener(eventListenerDetails);
        }
      }
    };
  }, [currentPage, submitReport]);

  useEffect(() => {
    const invalidCommentElement = document.querySelector(
      `#${CustomElementId.InvalidReportComment}`
    );
    if (!invalidCommentElement) {
      return;
    }
    if (isCommentValid) {
      invalidCommentElement.classList.add("abs-hidden");
    } else {
      invalidCommentElement.classList.remove("abs-hidden");
    }
  }, [isCommentValid]);

  useEffect(() => {
    if (isSuccess && isDone) {
      toggleElementHidden(CustomElementId.Report);
      toggleElementHidden(CustomElementId.ReportSuccess);
    } else if (!isSuccess && isDone) {
      toggleElementHidden(CustomElementId.Report);
      toggleElementHidden(CustomElementId.ReportFailure);
    }
  }, [isSuccess, isDone]);
};

const useCustomPasswordPage = (currentPage: string | undefined, pageType: PageType): void => {
  const [downloadId, setDownloadId] = useState<number>(0);
  const [filename, setFilename] = useState<string>("");
  const [isDone, setIsDone] = useState<boolean>(false);
  const [password, setPassword] = useState<string>("");
  const configuration = useHmcConfiguration();

  const submitPassword = useSubmitPassword(password, downloadId, filename, setIsDone);

  const onPasswordChange = (event?: Event | InputEvent): void => {
    const target = event?.target as HTMLInputElement;
    setPassword(target.value);
  };

  useEffect(() => {
    if (pageType !== PageType.PasswordInputPage) {
      return;
    }
    const { downloadId, filename } = getQueryParameters();
    if (
      downloadId === undefined ||
      filename === undefined ||
      isArray(downloadId) ||
      isArray(filename)
    ) {
      return;
    }
    setDownloadId(downloadId as unknown as number);
    setFilename(filename);
  }, [setDownloadId, setFilename, downloadId, filename, pageType]);

  useEffect(() => {
    if (pageType !== PageType.PasswordInputPage) {
      return;
    }
    const addEventListeners = (): (EventListenerDetails | undefined)[] => {
      return [
        addEventListenerById(
          CustomElementId.PasswordFormPassword,
          onPasswordChange,
          EventType.Input
        ),
        addEventListenerById(CustomElementId.PasswordForm, submitPassword, EventType.Submit),
      ];
    };
    const eventsListenersDetails = addEventListeners();

    return () => {
      for (const eventListenerDetails of eventsListenersDetails) {
        if (eventListenerDetails) {
          removeEventListener(eventListenerDetails);
        }
      }
    };
  }, [currentPage, pageType, submitPassword]);

  useEffect(() => {
    if (pageType !== PageType.PasswordInputPage) {
      return;
    }
    if (!isDone) {
      return;
    }
    window.close();
  }, [isDone, pageType]);

  useEffect(() => {
    if (pageType !== PageType.PasswordInputPage) {
      return;
    }
    if (configuration.encryptedFilePasswordSubmissionMaxTimeInMS) {
      setTimeout(() => {
        setPassword("");
        submitPassword();
      }, configuration.encryptedFilePasswordSubmissionMaxTimeInMS);
    }
  }, [configuration, submitPassword, setPassword, setIsDone, pageType]);

  useEffect(() => {
    if (pageType !== PageType.PasswordInputPage) {
      return;
    }
    function beforeUnload(): void {
      if (isDone) {
        return;
      }

      setPassword("");
      submitPassword();
    }
    window.addEventListener("beforeunload", beforeUnload);

    return () => {
      window.removeEventListener("beforeunload", beforeUnload);
    };
  }, [isDone, pageType, setPassword, submitPassword]);
};

export const CustomHtmlPage = ({
  pageType,
  originalPage,
}: CustomHtmlPageProperties): JSX.Element => {
  const customHtml = useCustomHtmlPage(pageType);
  const [currentPage, setCurrentPage] = useState<string>("");

  useEffect(() => {
    if (customHtml) {
      setCurrentPage(appendStyle(customHtml) ?? "");
    }
  }, [customHtml]);

  const continueAnyway = useContinueAnyway();

  const backToSafety = useBackToSafety();

  const toggleReportClicked = (): void => {
    toggleElementHidden(CustomElementId.Index);
    toggleElementHidden(CustomElementId.Report);
  };

  const reloadPageClicked = (): void => {
    window.location.reload();
  };

  const toggleDetailsClicked = (): void => {
    toggleElementHidden(CustomElementId.Details);
  };

  const closePageClicked = (): void => {
    window.close();
  };

  useCustomReportPage(currentPage);

  useCustomPasswordPage(currentPage, pageType);

  useEffect(() => {
    const addEventListeners = (): (EventListenerDetails | undefined)[] => {
      return [
        addEventListenerById(CustomElementId.ContinueAnyway, continueAnyway, EventType.Click),
        addEventListenerById(CustomElementId.BackToSafety, backToSafety, EventType.Click),
        addEventListenerById(CustomElementId.ShowReport, toggleReportClicked, EventType.Click),
        addEventListenerById(CustomElementId.CloseReport, toggleReportClicked, EventType.Click),
        addEventListenerById(CustomElementId.ReloadPage, reloadPageClicked, EventType.Click),
        addEventListenerById(CustomElementId.ToggleDetails, toggleDetailsClicked, EventType.Click),
        addEventListenerById(CustomElementId.Logo, reloadPageClicked, EventType.Click),
        addEventListenerById(CustomElementId.ClosePage, closePageClicked, EventType.Click),
      ];
    };
    const eventsListenersDetails = addEventListeners();

    return () => {
      for (const eventListenerDetails of eventsListenersDetails) {
        if (eventListenerDetails) {
          removeEventListener(eventListenerDetails);
        }
      }
    };
  }, [customHtml, currentPage, continueAnyway, backToSafety]);

  useEffect(() => {
    const autoFillInputs = (): void => {
      const {
        dom,
        url,
        categories,
        fileName,
        filename,
        reasonText,
        extensionDisplayName,
        isSuccessfulDisabling,
        riskLevel,
      } = getQueryParameters();
      setElementValue(CustomElementId.WebsiteUrl, url);
      setElementValue(CustomElementId.WebsiteDomain, dom);
      setElementValue(CustomElementId.Category, categories);
      setElementValue(CustomElementId.Filename, fileName ?? filename);
      setElementValue(CustomElementId.UnscannableReason, reasonText);
      setElementValue(CustomElementId.TPEName, extensionDisplayName);
      setElementValue(CustomElementId.TPERiskScore, riskLevel);
      if (isSuccessfulDisabling === "true") {
        setElementValue(CustomElementId.TPEDisabledReason, "disabled");
        setElementValue(CustomElementId.TPEDisabledNextAction);
      } else {
        setElementValue(CustomElementId.TPEDisabledReason, "not permitted");
        setElementValue(CustomElementId.TPEDisabledNextAction, "Please uninstall the extension.");
      }
    };
    autoFillInputs();
  }, [currentPage]);

  useEffect(() => {
    const { categories, blockPageDetectionMode } = getQueryParameters();
    if (categories) {
      toggleElementHidden(CustomElementId.ToggleDetails);
    }
    if (blockPageDetectionMode === "warn") {
      toggleElementHidden(CustomElementId.BlockFeatures);
      toggleElementHidden(CustomElementId.WarnFeatures);
      toggleElementHidden(CustomElementId.ContinueAnywaySection);
    }
  }, [currentPage]);

  if (customHtml === undefined) {
    return originalPage ? originalPage : <></>;
  } else if (customHtml === "") {
    return <></>;
  } else {
    return <div dangerouslySetInnerHTML={{ __html: currentPage }} />;
  }
};
