import { createStyles, makeStyles } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { ReactComponent as ErrorLogo } from "../assets/error-logo.svg";
import { BlockPage, BlockPageColor, BlockType } from "./block-page";
import { getQueryParameters } from "../utils/url";
import { TestId } from "../common/test-ids";

const useStyles = makeStyles(() =>
  createStyles({
    fileName: {
      wordBreak: "break-all",
    },
    subtitle: {
      fontWeight: 600,
      marginBottom: 20,
    },
  })
);

export const BlockUnscannableFile = (): JSX.Element => {
  const classes = useStyles();
  const [fileName, setFileName] = useState<string>();
  const [reasonText, setReasonText] = useState<string>();
  const [shouldReplaceMessageBody, setShouldReplaceMessageBody] = useState<boolean>();

  useEffect(() => {
    const { fileName, reasonText, shouldReplaceMessageBody } = getQueryParameters();
    setFileName(fileName as string);
    setReasonText(reasonText as string);
    setShouldReplaceMessageBody(shouldReplaceMessageBody as unknown as boolean);
  }, [setFileName, setReasonText, setShouldReplaceMessageBody]);

  return (
    <BlockPage
      icon={<ErrorLogo />}
      title="Downloaded file cannot be scanned"
      color={BlockPageColor.Red}
      subtitle={
        <div className={classes.subtitle} data-testid={TestId.UnscannableBlockPageSubtitle}>
          {shouldReplaceMessageBody && shouldReplaceMessageBody === true ? (
            reasonText
          ) : (
            <>
              The file{" "}
              <span className={classes.fileName} data-testid={TestId.UnscannableFileFilename}>
                {fileName}
              </span>{" "}
              {reasonText}
              , and therefore can&apos;t be scanned for malicious content.
              <br />
              Your download has been blocked.
              <br />
              Please contact your administrator for more information.
            </>
          )}
        </div>
      }
      blockType={BlockType.File}
      testid={TestId.UnscannableFileBlockPage}
    />
  );
};
