import browser from "browser-polyfill";
import { useEffect, useState, useCallback } from "react";
import { BlockPageDetectionMode, MessageKind, ReportFalsePositiveMessageResponse } from "common";
import { getQueryParameters } from "../utils/url";

export const useContinueAnyway = (): (() => void) => {
  const [continueAnywayCalled, setContinueAnywayCalled] = useState<boolean>(false);
  const [url, setUrl] = useState<string>();
  const [scanId, setScanId] = useState<string | undefined>();
  const [blockPageDetectionMode, setBlockPageDetectionMode] = useState<BlockPageDetectionMode>();

  useEffect(() => {
    const { url, scanId, blockPageDetectionMode } = getQueryParameters();
    setUrl(url as string);
    setScanId(scanId as string);
    setBlockPageDetectionMode(blockPageDetectionMode as BlockPageDetectionMode);
  }, [setUrl, setScanId, setBlockPageDetectionMode]);

  const continueAnyway = useCallback((): void => {
    if (!continueAnywayCalled) {
      if (scanId) {
        void browser.runtime.sendMessage({ kind: MessageKind.ReportFalsePositive, scanId });
      }
      void browser.tabs.query({ active: true, currentWindow: true }).then(result => {
        const tabId = result[0].id ?? -1;
        void browser.runtime.sendMessage({
          kind: MessageKind.BypassMaliciousPage,
          tabId,
          url,
          detectionMode: blockPageDetectionMode,
        });
        setContinueAnywayCalled(true);
      });
    }
  }, [continueAnywayCalled, blockPageDetectionMode, scanId, url]);

  return continueAnyway;
};

export const useBackToSafety = (): (() => void) => {
  const [backToSafetyCalled, setBackToSafetyCalled] = useState<boolean>(false);

  const backToSafety = useCallback((): void => {
    if (!backToSafetyCalled) {
      void browser.runtime.sendMessage({ kind: MessageKind.BackToSafety });
      setBackToSafetyCalled(true);
    }
  }, [backToSafetyCalled]);

  return backToSafety;
};

export const useToggleDetailsTab = (
  setIsDetailsTab?: React.Dispatch<React.SetStateAction<boolean>>,
  isDetailsTab?: boolean
): (() => void) => {
  const toggleDetailsTab = useCallback((): void => {
    if (setIsDetailsTab) {
      setIsDetailsTab(!isDetailsTab);
    }
  }, [isDetailsTab, setIsDetailsTab]);

  return toggleDetailsTab;
};

export const useSubmitReport = (
  comment: string,
  scanId: string,
  isCommentValid: boolean,
  setIsDone: React.Dispatch<React.SetStateAction<boolean>>,
  setIsSuccess: React.Dispatch<React.SetStateAction<boolean>>
): (() => void) => {
  const [isSent, setIsSent] = useState<boolean>(false);

  const submitReport = useCallback((): void => {
    if (!isSent && isCommentValid) {
      setIsSent(true);
      void browser.runtime
        .sendMessage({ kind: MessageKind.ReportFalsePositive, comment, scanId })
        .then((response: ReportFalsePositiveMessageResponse) => {
          setIsSuccess(response.success ?? false);
          setIsDone(true);
        });
    }
  }, [comment, isCommentValid, setIsDone, isSent, setIsSent, setIsSuccess, scanId]);

  return submitReport;
};

export const useSubmitPassword = (
  password: string,
  downloadId: number,
  filename: string,
  setIsDone: React.Dispatch<React.SetStateAction<boolean>>
): (() => void) => {
  const submitPassword = useCallback((): void => {
    void browser.runtime
      .sendMessage({
        kind: MessageKind.SetEncryptedFilePassword,
        password,
        downloadId,
        filename,
      })
      .then(() => {
        setIsDone(true);
      });
  }, [password, downloadId, filename, setIsDone]);

  return submitPassword;
};
