import React, { ReactNode, useCallback, useEffect, useState } from "react";
import useWindowSize from "../hooks/window-size";
import useHmcConfiguration from "../hooks/use-hmc-configuration";
import { ReportWebsiteClean } from "./report-website-clean";
import { getQueryParameters } from "../utils/url";
import { BlockPageDetectionMode } from "common";
import { BlockPageButtons, ButtonProperties } from "../components/block-page-buttons";
import { BlockPageHeader } from "../components/block-page-header";
import { BlockPageContainer } from "../components/block-page-container";
import { BlockPageDetails } from "../components/block-page-details";
import { BlockPageDescription } from "../components/block-page-description";
import { TestId } from "../common/test-ids";

export enum BlockPageColor {
  Gray = "#8C8C8C",
  Red = "#A20000",
  White = "#FFFFFF",
}

export enum BlockType {
  File = "file",
  MaliciousWebsite = "malicious-website",
  WebsiteRule = "website-rule",
  Extension = "extension",
}

export enum ContinueAnywayType {
  Link = "link",
  Button = "button",
}

export interface BlockPageProperties {
  allowBackToSafety?: boolean;
  continueAnyway?: ContinueAnywayType;
  allowReportingWebsite?: boolean;
  children?: ReactNode;
  icon: JSX.Element;
  title: string;
  subtitle: JSX.Element | string;
  blockType: BlockType;
  buttons?: ButtonProperties[];
  details?: JSX.Element | string;
  color?: BlockPageColor;
  secondaryColor?: BlockPageColor;
  detectionMode?: BlockPageDetectionMode;
  testid: TestId;
}

export const BlockPage = ({
  children,
  icon,
  title,
  subtitle,
  blockType,
  buttons,
  details,
  testid,
  continueAnyway,
  detectionMode,
  allowBackToSafety = false,
  allowReportingWebsite = false,
  color = BlockPageColor.Gray,
  secondaryColor = BlockPageColor.White,
}: BlockPageProperties): JSX.Element => {
  const windowSize = useWindowSize();
  const configuration = useHmcConfiguration();
  const [url, setUrl] = useState<string>();
  const [scanId, setScanId] = useState<string | undefined>();
  const [isDetailsTab, setIsDetailsTab] = useState<boolean>(false);
  const [isSmallWindow, setIsSmallWindow] = useState<boolean>(true);
  const [displayContinueAnyway, setDisplayContinueAnyway] = useState<boolean>(false);
  const [displayReportingWebsite, setDisplayReportingWebsite] = useState<boolean>(false);
  const [isReportAsSafePage, setIsReportAsSafePage] = useState<boolean>(false);
  const [isButtonsAtBottom, setIsButtonsAtBottom] = useState<boolean>(false);
  const colors = { primary: color, secondary: secondaryColor };

  useEffect(() => {
    const { url, scanId } = getQueryParameters();
    setUrl(url as string);
    setScanId(scanId as string);
  }, [setUrl, setScanId]);

  useEffect(() => {
    if (configuration) {
      setDisplayReportingWebsite(
        (configuration.allowReportingWebsiteAsClean ?? false) && allowReportingWebsite
      );

      setDisplayContinueAnyway(
        detectionMode === BlockPageDetectionMode.Warn && continueAnyway === ContinueAnywayType.Link
      );
    }
  }, [configuration, continueAnyway, allowReportingWebsite, blockType, detectionMode]);

  useEffect(() => {
    if (windowSize) {
      const isSmallWindow = windowSize.height <= 700;
      setIsSmallWindow(isSmallWindow);
      setIsButtonsAtBottom(isSmallWindow);
    }
  }, [windowSize, setIsSmallWindow]);

  useCallback((): void => {
    setIsDetailsTab(!isDetailsTab);
  }, [isDetailsTab]);

  return (
    <>
      {isReportAsSafePage ? (
        <ReportWebsiteClean
          goBack={() => setIsReportAsSafePage(false)}
          url={url ?? ""}
          scanId={scanId ?? ""}
          styleParameters={colors}
        />
      ) : (
        <BlockPageContainer styleParameters={colors} testid={testid}>
          <BlockPageHeader icon={icon} title={title} />
          {(!isSmallWindow || !isDetailsTab) && (
            <BlockPageDescription subtitle={subtitle} displayCustomLink blockType={blockType}>
              {children}
            </BlockPageDescription>
          )}
          <BlockPageButtons
            allowBackToSafety={allowBackToSafety}
            allowDetails={Boolean(details) || displayContinueAnyway || displayReportingWebsite}
            allowContinueAnyway={
              detectionMode === BlockPageDetectionMode.Warn &&
              continueAnyway === ContinueAnywayType.Button
            }
            isDetailsTab={isDetailsTab}
            setIsDetailsTab={setIsDetailsTab}
            styleParameters={{ ...colors, isButtonsAtBottom }}
            buttons={buttons}
          />
          {(!isSmallWindow || isDetailsTab) && (
            <BlockPageDetails
              isDetailsTab={isDetailsTab}
              details={details}
              scanId={scanId}
              setIsReportAsSafePage={setIsReportAsSafePage}
              displayReportingWebsite={displayReportingWebsite}
              displayContinueAnyway={displayContinueAnyway}
              styleParameters={{ color: colors.secondary }}
            />
          )}
        </BlockPageContainer>
      )}
    </>
  );
};
