import queryString, { ParsedUrlQuery } from "querystring";

export const getQueryParameters = (): ParsedUrlQuery => {
  return queryString.parse((window.location.search || "").replace(/^\?/, ""));
};

export const getUrl = (): string => {
  const { url } = getQueryParameters();
  if (!url) {
    return "";
  }

  try {
    const urlObject = new URL(url as string);
    if (urlObject.protocol === "file:") {
      return urlObject.pathname;
    }
    if (urlObject.protocol === "blob:") {
      return urlObject.origin;
    }
    return !urlObject.hostname ? (url as string) : urlObject.hostname;
  } catch {
    return url as string;
  }
};
