import { storage } from "browser-polyfill";
import { useEffect, useState } from "react";

const localSettingsKeys = ["lastUser", "idpSigninUrl", "signInRedirectCountdown"];

interface LocalSettings {
  lastUser: string;
  idpSigninUrl: string;
  signInRedirectCountdown?: number;
}

export const defaultLocalSettings: LocalSettings = {
  lastUser: "",
  idpSigninUrl: "",
};

const useLocalSettings = (): LocalSettings | undefined => {
  const [localSettings, setLocalSettings] = useState<LocalSettings | undefined>();
  useEffect(() => {
    const refreshSettings = (): void => {
      void storage.local.get(localSettingsKeys).then(settings => {
        setLocalSettings({ ...defaultLocalSettings, ...(settings as LocalSettings) });
      });
    };
    storage.onChanged.addListener(refreshSettings);
    refreshSettings();
    return () => {
      storage.onChanged.removeListener(refreshSettings);
    };
  }, [setLocalSettings]);

  return localSettings;
};

export default useLocalSettings;
