import browser, { Storage } from "browser-polyfill";
import React, { useEffect, useState, useCallback } from "react";
import queryString from "querystring";

import createStyles from "@material-ui/core/styles/createStyles";
import makeStyles from "@material-ui/core/styles/makeStyles";
import { ReactComponent as BrowserImage } from "../assets/browser-and-shield.svg";
import { ReactComponent as Complete } from "../assets/complete.svg";

import SigninIdentifyUser, { SigninIdentifyResult } from "../components/signin-identify-user";
import SigninVerifyUser from "../components/signin-verify-user";
import SignInWelcomeMessage from "../components/signin-welcome-message";
import { TestId } from "../common/test-ids";
import { GetTokenResponse, MessageKind } from "common";

const useStyles = makeStyles(() =>
  createStyles({
    browserImage: {
      height: "40%",
      maxWidth: 500,
    },
    content: {
      alignItems: "center",
      display: "flex",
      flexDirection: "row",
      fontFamily: "Circular Pro",
      height: "100vh",
    },
    signInContent: {
      maxWidth: 500,
      textAlign: "center",
    },
    signedIn: {
      color: "#1B1948",
      fontSize: 30,
      fontWeight: 500,
      letterSpacing: -1.509,
      marginTop: 30,
    },
    signedInTip: {
      color: "#4F4D5C",
      fontSize: 16,
      fontWeight: 400,
      marginTop: 30,
    },
    identifyForm: {
      alignItems: "center",
      backgroundColor: "#EFF2F6",
      color: "#4F4D5C",
      display: "flex",
      flexDirection: "column",
      fontSize: 20,
      justifyContent: "space-evenly",
      height: "inherit",
      padding: "0px 30px",
      width: "60%",
    },
  })
);

export const Signin = (): JSX.Element => {
  const classes = useStyles();
  const [signinIdentifyResult, setSigninIdentifyResult] = useState<SigninIdentifyResult>();
  const queryParams = queryString.parse((window.location.search || "").replace(/^\?/, ""));
  const [isDone, setIsDone] = useState<boolean>(queryParams.done === "true");

  const handleSigninComplete = useCallback((): void => {
    setIsDone(true);
    const preSignInUrl = queryParams.preSignInUrl as string;
    if (preSignInUrl) {
      setTimeout(() => {
        window.location.href = preSignInUrl;
      }, 3000);
    }
  }, [setIsDone, queryParams.preSignInUrl]);

  useEffect(() => {
    const checkToken = (): void => {
      void browser.runtime
        .sendMessage({ kind: MessageKind.GetToken })
        .then(({ jwtToken }: GetTokenResponse) => {
          if (jwtToken !== undefined) {
            handleSigninComplete();
          }
        });
    };
    const storageListener = (
      changes: Record<string, Storage.StorageChange>,
      namespace: string
    ): void => {
      if (namespace === "local" && changes.jwtToken) {
        checkToken();
      }
    };
    browser.storage.onChanged.addListener(storageListener);
    checkToken();
    return () => {
      browser.storage.onChanged.removeListener(storageListener);
    };
  }, [handleSigninComplete]);

  return (
    <div className={classes.content}>
      <SignInWelcomeMessage showBackButton={Boolean(signinIdentifyResult && !isDone)} />
      <div className={classes.identifyForm}>
        <BrowserImage className={classes.browserImage} />
        <div className={classes.signInContent}>
          {!isDone &&
            (signinIdentifyResult ? (
              <SigninVerifyUser
                email={signinIdentifyResult?.email}
                cognitoUser={signinIdentifyResult?.cognitoUser}
                handleSigninComplete={handleSigninComplete}
                userPoolClientId={signinIdentifyResult.userPoolClientId}
                userPoolId={signinIdentifyResult.userPoolId}
              />
            ) : (
              <SigninIdentifyUser setResult={setSigninIdentifyResult} />
            ))}
          {isDone && (
            <>
              <Complete />
              <h2 className={classes.signedIn} data-testid={TestId.SigninDone}>
                That&apos;s it, you are now protected!
              </h2>
              <div className={classes.signedInTip} data-testid={TestId.SigninTipDone}>
                Tip: restart the browser to enable protection for existing tabs
              </div>
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default Signin;
