import { Button, createStyles, makeStyles } from "@material-ui/core";
import React from "react";
import { TestId } from "../common/test-ids";
import { useBackToSafety, useContinueAnyway, useToggleDetailsTab } from "../hooks/block-page-hooks";
import { BlockPageColor } from "../pages/block-page";
import { BlockPageButtonsContainer } from "./block-page-buttons-container";

interface BlockPageButtonProperties {
  label: string;
  onClick?: () => void;
  styleParameters: ButtonStyleParameters;
  testid: TestId;
}

interface ButtonStyleParameters {
  primary: BlockPageColor;
  secondary: BlockPageColor;
  inverted?: boolean;
}

const useStyles = makeStyles(() => {
  return createStyles({
    button: ({ inverted, primary, secondary }: ButtonStyleParameters) => ({
      backgroundColor: inverted ? secondary : "transparent",
      border: `2px solid ${secondary}`,
      borderRadius: 0,
      color: inverted ? primary : secondary,
      fontSize: 12,
      textTransform: "uppercase",
      width: 180,
      "&:hover": {
        backgroundColor: inverted ? primary : secondary,
        color: inverted ? secondary : primary,
      },
    }),
  });
});

const BlockPageButton = ({
  label,
  onClick,
  styleParameters,
  testid,
}: BlockPageButtonProperties): JSX.Element => {
  const { button } = useStyles(styleParameters);
  return (
    <Button className={button} onClick={onClick} data-testid={testid}>
      {label}
    </Button>
  );
};

interface DetailsToggleButtonProperties {
  isDetailsTab?: boolean;
  setIsDetailsTab?: React.Dispatch<React.SetStateAction<boolean>>;
  styleParameters: ButtonStyleParameters;
}

const DetailsToggleButton = ({
  setIsDetailsTab,
  isDetailsTab,
  styleParameters,
}: DetailsToggleButtonProperties): JSX.Element => {
  const toggleDetailsTab = useToggleDetailsTab(setIsDetailsTab, isDetailsTab);
  return (
    <BlockPageButton
      label={isDetailsTab ? "Hide details" : "Show details"}
      onClick={toggleDetailsTab}
      styleParameters={{ ...styleParameters }}
      testid={TestId.DetailsToggleButton}
    />
  );
};

interface ButtonStyleProperties {
  styleParameters: ButtonStyleParameters;
}

const BackToSafetyButton = ({ styleParameters }: ButtonStyleProperties): JSX.Element => {
  return (
    <BlockPageButton
      styleParameters={{ ...styleParameters, inverted: true }}
      label="Back to safety"
      onClick={useBackToSafety()}
      testid={TestId.BackToSafetyButton}
    />
  );
};

const ContinueAnywayButton = ({ styleParameters }: ButtonStyleProperties): JSX.Element => {
  return (
    <BlockPageButton
      styleParameters={{ ...styleParameters }}
      label="Continue to Website"
      onClick={useContinueAnyway()}
      testid={TestId.ContinueAnywayButton}
    />
  );
};

interface StyleParameters {
  primary: BlockPageColor;
  secondary: BlockPageColor;
  isButtonsAtBottom: boolean;
}

export interface ButtonProperties {
  label: string;
  onClick?: () => void;
  inverted?: boolean;
  testid: TestId;
}

export interface BlockPageButtonsProperties {
  allowBackToSafety?: boolean;
  allowDetails?: boolean;
  allowContinueAnyway?: boolean;
  isDetailsTab?: boolean;
  setIsDetailsTab?: React.Dispatch<React.SetStateAction<boolean>>;
  styleParameters: StyleParameters;
  buttons?: ButtonProperties[];
}

export const BlockPageButtons = ({
  allowBackToSafety,
  allowDetails,
  allowContinueAnyway,
  buttons,
  isDetailsTab,
  setIsDetailsTab,
  styleParameters,
}: BlockPageButtonsProperties): JSX.Element => {
  const buttonsStyleParameters = {
    primary: styleParameters.primary,
    secondary: styleParameters.secondary,
  };
  return (
    <BlockPageButtonsContainer isButtonsAtBottom={styleParameters.isButtonsAtBottom}>
      {allowDetails && (
        <DetailsToggleButton
          isDetailsTab={isDetailsTab}
          setIsDetailsTab={setIsDetailsTab}
          styleParameters={buttonsStyleParameters}
        />
      )}
      {allowContinueAnyway && <ContinueAnywayButton styleParameters={buttonsStyleParameters} />}
      {allowBackToSafety && <BackToSafetyButton styleParameters={buttonsStyleParameters} />}
      {buttons?.map(({ label, onClick, inverted, testid }) => (
        <BlockPageButton
          label={label}
          key={label}
          onClick={onClick}
          styleParameters={{
            ...buttonsStyleParameters,
            inverted,
          }}
          testid={testid}
        />
      ))}
    </BlockPageButtonsContainer>
  );
};
