import browser from "browser-polyfill";
import React, { useCallback, useEffect, useState } from "react";
import { MessageKind } from "common";
import { makeStyles, createStyles, Typography, Button, TextField } from "@material-ui/core";
import { PpTextLogo } from "../hooks/pp-text-logo";
import { ReactComponent as BlockPageBackground } from "../assets/block-page-background.svg";
import { ReactComponent as GetPasswordIcon } from "../assets/get-password-icon.svg";
import useHmcConfiguration from "../hooks/use-hmc-configuration";
import { useSubmitPassword } from "../hooks/block-page-hooks";

const useStyles = makeStyles(() =>
  createStyles({
    button: {
      background: "#FF5400",
      display: "flex",
      flexDirection: "row",
      color: "#FFFFFF",
      fontSize: 13,
      fontWeight: 500,
      padding: "14px 22px 13px 22px",
      top: 20,
      fontFamily: "Circular Pro",
      width: 450,
      marginTop: 20,
      letterSpacing: 1.56,
      lineHeight: "16px",
    },
    input: {
      border: "1px solid #000033",
      boxSizing: "border-box",
      borderRadius: 4,
      marginTop: 30,
      width: 450,
      "& .MuiInputBase-input": {
        height: 40,
        marginLeft: 16,
      },
      height: 50,
    },
    ppLogo: {
      marginTop: 40,
    },
    title: {
      color: "#1B1948",
      fontSize: 34,
      fontWeight: 700,
      marginTop: 50,
      lineHeight: "36px",
      fontFamily: "Circular Pro",
      alignSelf: "stretch",
      alignItems: "flex-start",
    },
    subtitle: {
      fontSize: 18,
      fontWeight: 450,
      color: "#4F4D5C",
      marginTop: 40,
      fontFamily: "Circular Pro",
      lineHeight: "26px",
    },
    content: {
      alignItems: "center",
      display: "flex",
      flexDirection: "row",
      fontFamily: "Circular Pro, Arial",
      height: "100vh",
    },
    description: {
      backgroundColor: "#EFF2F6",
      color: "#4F4D5C",
      display: "flex",
      flexDirection: "column",
      fontSize: 20,
      justifyContent: "center",
      height: "inherit",
      width: "80%",
    },
    descriptionWrapper: {
      maxWidth: 800,
      margin: "0% 14%",
    },
    left: {
      alignItems: "start",
      display: "flex",
      height: "inherit",
      flexDirection: "column",
      justifyContent: "space-between",
      padding: "0px 60px",
      width: "20%",
    },
    decorations: {
      height: "70%",
      marginTop: "auto",
    },
  })
);

interface EncryptedFilePasswordPromptProperties {
  downloadId: number;
  filename: string;
}

export const EncryptedFilePasswordPrompt: React.FC<EncryptedFilePasswordPromptProperties> = ({
  downloadId,
  filename,
}: EncryptedFilePasswordPromptProperties) => {
  const classes = useStyles();
  const [password, setPassword] = useState<string>("");
  const [isDone, setIsDone] = useState<boolean>(false);
  const configuration = useHmcConfiguration();

  useEffect(() => {
    if (!isDone) {
      return;
    }
    window.close();
  }, [isDone]);

  const onPasswordChange = (event: React.ChangeEvent<HTMLInputElement>): void => {
    setPassword(event.target.value);
  };

  const sendEncryptedFilePassword = useCallback((): void => {
    void browser.runtime.sendMessage({
      kind: MessageKind.SetEncryptedFilePassword,
      password,
      downloadId,
      filename,
    });
  }, [password, downloadId, filename]);

  const onSubmitClick = useSubmitPassword(password, downloadId, filename, setIsDone);

  useEffect(() => {
    if (configuration.encryptedFilePasswordSubmissionMaxTimeInMS) {
      setTimeout(() => {
        setPassword("");
        sendEncryptedFilePassword();
        setIsDone(true);
      }, configuration.encryptedFilePasswordSubmissionMaxTimeInMS);
    }
  }, [configuration, sendEncryptedFilePassword, setPassword, setIsDone]);

  useEffect(() => {
    function beforeUnload(): void {
      if (isDone) {
        return;
      }

      setPassword("");
      sendEncryptedFilePassword();
    }
    window.addEventListener("beforeunload", beforeUnload);

    return () => {
      window.removeEventListener("beforeunload", beforeUnload);
    };
  }, [isDone, setPassword, sendEncryptedFilePassword]);

  return (
    <div className={classes.content}>
      <div className={classes.left}>
        <PpTextLogo className={classes.ppLogo} />
        <BlockPageBackground className={classes.decorations} />
      </div>
      <div className={classes.description}>
        <div className={classes.descriptionWrapper}>
          <GetPasswordIcon />
          <Typography component="div" className={classes.title}>
            Password required to scan your downloaded file
          </Typography>
          {!isDone && (
            <>
              <Typography component="div" className={classes.subtitle}>
                <strong>{filename}</strong> is a password-protected file. Please provide the
                password so that the file can be scanned for malicious content.
              </Typography>
              <TextField
                onChange={onPasswordChange}
                className={classes.input}
                required
                type="password"
                placeholder="Password"
              ></TextField>
              <Button
                variant="contained"
                color="primary"
                onClick={onSubmitClick}
                className={classes.button}
                disableElevation
                disabled={password.length === 0}
              >
                CONTINUE
              </Button>
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default EncryptedFilePasswordPrompt;
