import React, { useCallback, useEffect, useState } from "react";

import createStyles from "@material-ui/core/styles/createStyles";
import makeStyles from "@material-ui/core/styles/makeStyles";
import TextField from "@material-ui/core/TextField/TextField";
import Button from "@material-ui/core/Button/Button";
import CancelIcon from "@material-ui/icons/Cancel";
import CircularProgress from "@material-ui/core/CircularProgress/CircularProgress";
import { CognitoUser } from "amazon-cognito-identity-js";
import { SigninIdentifyError, SigninIdentifyResponseKind, UserAuthorizationStatus } from "common";
import queryString from "querystring";

import { TestId } from "../common/test-ids";
import { emailRegexp } from "../common/email";
import { cognitoSigninIdentifyUser } from "../utils/cognito";

const identifyErrors = {
  [SigninIdentifyError.NotFound]:
    "Sorry, we couldn't find this account." +
    " Please enter the email address that you signed up with, or with which you were invited.",
  [SigninIdentifyError.InternalError]:
    "Internal server error." +
    " Please enter the email address that you signed up with, or with which you were invited.",
  [SigninIdentifyError.IdpUser]:
    "IDP users are not supported." +
    " Please enter the email address that you signed up with, or with which you were invited.",
  [SigninIdentifyError.TenantDisabled]:
    "The tenant was disabled." +
    " Please enter the email address that you signed up with, or with which you were invited.",
};

const getMessageDescription = (message: string): string => {
  switch (message) {
    case UserAuthorizationStatus.VerificationSessionExpired:
      return "There were too many failed attempts or your code has expired. Please try again.";
    default:
      return "";
  }
};

const useStyles = makeStyles(() =>
  createStyles({
    alert: {
      color: "#FF0000",
      marginBottom: 20,
    },
    contents: {
      alignItems: "center",
      display: "flex",
      flexDirection: "column",
    },
    emailTextField: {
      fontFamily: "Circular Pro",
      fontSize: 24,
      marginTop: 40,
      width: "100%",
      "& .MuiInputBase-input": {
        fontFamily: "Circular Pro",
        color: "#000033",
      },
      "& .MuiInputLabel-root": {
        fontFamily: "Circular Pro",
        color: "#000033",
      },
      "& .MuiOutlinedInput-root": {
        "& fieldset": {
          borderColor: "#000033",
        },
        "&:hover fieldset": {
          borderColor: "#000033",
        },
        "&.Mui-focused fieldset": {
          borderColor: "#000033",
        },
      },
    },
    emailTextFieldDescription: {
      textAlign: "center",
      width: "80%",
    },
    errorIcon: {
      color: "#CB3D3D",
      marginBottom: 4,
      marginRight: 6,
      verticalAlign: "middle",
    },
    nextButton: {
      alignSelf: "center",
      fontWeight: 700,
      height: 50,
      letterSpacing: 1.56,
      marginTop: 30,
      width: "100%",
    },
    progress: {
      color: "#FFFFFF",
    },
  })
);

export interface SigninIdentifyResult {
  email: string;
  cognitoUser: CognitoUser;
  userPoolId: string;
  userPoolClientId: string;
}

interface SigninIdentifyUserProperties {
  setResult: (result: SigninIdentifyResult) => void;
}

export const SigninIdentifyUser = ({ setResult }: SigninIdentifyUserProperties): JSX.Element => {
  const classes = useStyles();
  const [email, setEmail] = useState("");
  const [isValid, setIsValid] = useState(true);
  const [emailError, setEmailError] = useState<string | undefined>();
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState<string>();

  useEffect(() => {
    const { message } = queryString.parse((window.location.search || "").replace(/^\?/, ""));
    setMessage(message as string);
  }, [setMessage]);

  const onEmailChange = (event: React.ChangeEvent<HTMLInputElement>): void => {
    setIsValid(true);
    setEmailError("");
    setEmail(event.target.value);
  };

  const handleKeyDown = (event: React.KeyboardEvent<HTMLInputElement>): void => {
    if (event.key === "Enter" && isValid) {
      onNextClick();
    }
  };

  const identifyUser = useCallback(() => {
    void cognitoSigninIdentifyUser(email).then(signinResult => {
      switch (signinResult.kind) {
        case SigninIdentifyResponseKind.Error: {
          setLoading(false);
          setEmailError(identifyErrors[signinResult.error]);
          setIsValid(false);
          return;
        }
        case SigninIdentifyResponseKind.CognitoUserPool: {
          setResult({
            email,
            cognitoUser: signinResult.cognitoUser,
            userPoolClientId: signinResult.userPoolClientId,
            userPoolId: signinResult.userPoolId,
          });
          return;
        }
        case SigninIdentifyResponseKind.SigninUrl: {
          window.location.href = signinResult.signinUrl;
          return;
        }
      }
    });
  }, [email, setResult]);

  const onNextClick = useCallback(() => {
    if (loading) {
      return;
    }
    if (!email || !emailRegexp.test(email.trim())) {
      setIsValid(false);
      setEmailError("Please enter a valid email address");
      return;
    }
    setIsValid(true);
    setEmailError("");
    setLoading(true);
    identifyUser();
  }, [email, identifyUser, loading]);

  return (
    <div className={classes.contents}>
      {message && (
        <div className={classes.alert} data-testid={TestId.SigninIdentifyUserError}>
          <CancelIcon className={classes.errorIcon} />
          {getMessageDescription(message)}
        </div>
      )}
      <div
        className={classes.emailTextFieldDescription}
        data-testid={TestId.SigninIdentifyUserDescription}
      >
        Please sign in with your work email address to activate the extension
      </div>
      <TextField
        className={classes.emailTextField}
        error={!isValid}
        fullWidth
        helperText={
          <span data-testid={TestId.SigninIdentifyUserEmailInputHelperText}>{emailError}</span>
        }
        inputProps={{
          "data-testid": TestId.SigninIdentifyUserEmailInput,
        }}
        spellCheck={false}
        label="Email address"
        onKeyDown={handleKeyDown}
        onChange={onEmailChange}
        type="email"
        variant="outlined"
      />
      <Button
        color="primary"
        disableElevation
        data-testid={TestId.SigninIdentifyUserNextButton}
        onClick={onNextClick}
        className={classes.nextButton}
        variant="contained"
      >
        {loading ? <CircularProgress size={24} className={classes.progress} /> : "CONTINUE"}
      </Button>
    </div>
  );
};

export default SigninIdentifyUser;
