import React, { useState } from "react";

import createStyles from "@material-ui/core/styles/createStyles";
import makeStyles from "@material-ui/core/styles/makeStyles";
import { DownloadProcessInfo, store } from "common";
import { liveQuery } from "dexie";
import DownloadDataCard from "../components/download-data-card";

const useStyles = makeStyles(() =>
  createStyles({
    content: {
      marginTop: 30,
      marginLeft: 30,
    },
    title: {
      fontFamily: "Circular Pro",
      fontSize: 30,
      padding: 30,
    },
  })
);

const mostRecentDownloads = liveQuery(() =>
  store.items.orderBy("timestamps.createdAt").reverse().limit(100).toArray()
);

export const DownloadManager = (): JSX.Element => {
  const [recentDownloads, setRecentDownloads] = useState<DownloadProcessInfo[] | undefined>();
  mostRecentDownloads.subscribe({
    next: (downloads: DownloadProcessInfo[]) => {
      setRecentDownloads(downloads);
    },
  });

  const classes = useStyles();

  return (
    <div>
      <h1 className={classes.title}> Download Manager </h1>
      <div className={classes.content}>
        {recentDownloads &&
          recentDownloads.map(recentDownload => {
            return (
              <DownloadDataCard downloadProcessInfo={recentDownload} key={recentDownload.id} />
            );
          })}
      </div>
    </div>
  );
};

export default DownloadManager;
