import React, { useEffect, useState } from "react";

import createStyles from "@material-ui/core/styles/createStyles";
import makeStyles from "@material-ui/core/styles/makeStyles";

import SignInWelcomeMessage from "../components/signin-welcome-message";
import { ReactComponent as BrowserImage } from "../assets/browser-and-shield.svg";
import useLocalSettings from "../hooks/use-local-settings";
import { TestId } from "../common/test-ids";

const WAITING_TIME_IN_SECONDS = 5;
const ONE_SECOND_IN_MILLISECONDS = 1000;

const useStyles = makeStyles(() =>
  createStyles({
    browserImage: {
      height: "40%",
      maxWidth: 500,
      marginBottom: "100px",
    },
    content: {
      alignItems: "center",
      display: "flex",
      flexDirection: "row",
      fontFamily: "Circular Pro",
      height: "100vh",
    },
    waitingSection: {
      alignItems: "center",
      backgroundColor: "#EFF2F6",
      color: "#4F4D5C",
      display: "flex",
      flexDirection: "column",
      justifyContent: "center",
      height: "inherit",
      padding: "0px 30px",
      width: "60%",
      textAlign: "center",
      fontWeight: 500,
    },
    waitingSectionTitle: {
      color: "#1B1948",
      fontSize: 30,
      letterSpacing: -1.509,
      marginBottom: "40px",
    },
    waitingSectionContent: {
      fontSize: 18,
    },
    waitingCounter: {
      color: "#FF5400",
    },
  })
);

export const SigninRedirectingPage = (): JSX.Element => {
  const classes = useStyles();

  const [secondsLeft, setSecondsLeft] = useState<number>(WAITING_TIME_IN_SECONDS);
  const [signinUrl, setSigninUrl] = useState<string>("");
  const localSettings = useLocalSettings();

  useEffect(() => {
    if (localSettings?.idpSigninUrl) {
      setSigninUrl(localSettings.idpSigninUrl);
    }
  }, [localSettings?.idpSigninUrl]);

  useEffect(() => {
    if (typeof localSettings?.signInRedirectCountdown === "number") {
      setSecondsLeft(localSettings.signInRedirectCountdown);
    }
  }, [localSettings?.signInRedirectCountdown]);

  useEffect(() => {
    if (!signinUrl) {
      return;
    }
    if (secondsLeft > 1) {
      setTimeout(() => {
        setSecondsLeft(currentSecondsLeft => currentSecondsLeft - 1);
      }, ONE_SECOND_IN_MILLISECONDS);
    } else {
      window.location.href = signinUrl;
    }
  }, [secondsLeft, signinUrl]);

  return (
    <div className={classes.content}>
      <SignInWelcomeMessage />
      <div className={classes.waitingSection}>
        <BrowserImage className={classes.browserImage} />
        <div>
          <div className={classes.waitingSectionTitle} data-testid={TestId.WaitingSectionTitle}>
            Sign in with your work credentials to activate the extension
          </div>
          <div className={classes.waitingSectionContent} data-testid={TestId.WaitingSectionContent}>
            {"You'll be redirected to your work login page in "}
            <span className={classes.waitingCounter}>
              {secondsLeft} second{secondsLeft > 1 ? "s" : ""}...
            </span>
          </div>
        </div>
      </div>
    </div>
  );
};
