import browser from "browser-polyfill";
import React, { useCallback, useEffect, useReducer } from "react";

import { LocalStorageContext, LocalStorageType } from "../contexts/local-storage";

interface LocalStorageProviderProperties {
  children?: React.ReactNode;
}

function localStorageReducer(
  previousState: LocalStorageType,
  newState: LocalStorageType
): LocalStorageType {
  return {
    ...previousState,
    ...newState,
  };
}

export const LocalStorageProvider = ({ children }: LocalStorageProviderProperties): JSX.Element => {
  const [localStorage, dispatchLocalStorage] = useReducer(localStorageReducer, {});

  const refresh = useCallback(
    (changes: Record<string, browser.Storage.StorageChange>): void => {
      const newValues = Object.fromEntries(
        Object.entries(changes)
          .filter(([_key, change]) => change)
          .map(([key, change]) => [key, change.newValue] as [string, unknown])
          .filter(([_key, value]) => value !== undefined)
      );
      dispatchLocalStorage(newValues);
    },
    [dispatchLocalStorage]
  );

  useEffect(() => {
    browser.storage.onChanged.addListener(refresh);
    void browser.storage.local.get().then(dispatchLocalStorage);
    return () => {
      browser.storage.onChanged.removeListener(refresh);
    };
  }, [refresh]);

  return (
    <LocalStorageContext.Provider value={localStorage}>{children}</LocalStorageContext.Provider>
  );
};
