import React, { createRef, useEffect } from "react";

import { createStyles, makeStyles, OutlinedInput } from "@material-ui/core";
import isNaN from "lodash/isNaN";
import times from "lodash/times";

import { TestId } from "../common/test-ids";

interface CodeInputProperties {
  autoFocus?: boolean;
  className?: string;
  disabled?: boolean;
  onChange: (digits: string[]) => void;
  onSubmit?: () => void;
  digits: string[];
  resetFocusOnClear?: boolean;
  onInvalidDigit?: (invalidDigit: string) => void;
}

const useStyles = makeStyles(() =>
  createStyles({
    wrapper: {
      display: "flex",
      flexDirection: "row",
      justifyContent: "space-between",
      width: "100%",
    },
  })
);

const handleFocus = (event: React.FocusEvent<HTMLInputElement>): void => {
  event.target.select();
};

const BACKSPACE_KEY = 8;
const ENTER_KEY = 13;

const CodeInput: React.FC<CodeInputProperties> = ({
  autoFocus,
  className,
  digits,
  disabled,
  onChange,
  onInvalidDigit,
  onSubmit,
  resetFocusOnClear,
}: CodeInputProperties) => {
  const classes = useStyles();
  const digitReferences: React.RefObject<HTMLInputElement>[] = times(digits.length, () =>
    createRef<HTMLInputElement>()
  );

  const handleKeyDown = (event: React.KeyboardEvent<HTMLInputElement>, index: number): void => {
    if (event.keyCode === ENTER_KEY && index === digits.length - 1) {
      onSubmit?.();
      return;
    }
    if (event.keyCode === BACKSPACE_KEY) {
      const updatedDigits = [...digits];
      updatedDigits[index] = "";
      onChange(updatedDigits);
      if (index > 0) {
        digitReferences[index - 1].current?.focus();
      }
    }
  };

  useEffect(() => {
    if (digits[0] === "" && resetFocusOnClear) {
      digitReferences[0].current?.focus();
    }
  }, [digits, digitReferences, resetFocusOnClear]);

  const handleDigitChange = (digit: string, digitIndex: number): void => {
    if (isNaN(Number(digit))) {
      onInvalidDigit?.(digit);
      return;
    }
    const updatedDigits: string[] = [...digits];
    updatedDigits[digitIndex] = digit;
    onChange(updatedDigits);

    if (digit.length > 1) {
      // handle pasting code from email
      const digitValues: string[] = digit.split("");
      for (const [valueIndex, digit] of digitValues.entries()) {
        const index = digitIndex + valueIndex;
        if (index >= updatedDigits.length) {
          break;
        }
        updatedDigits[index] = digit;
      }
    }
    if (digit === "") {
      return;
    }

    const nextInput = digitReferences[digitIndex + digit.length];
    if (nextInput) {
      nextInput.current?.focus();
    }
  };

  return (
    <div className={classes.wrapper}>
      {digits.map((digit, index) => (
        <OutlinedInput
          autoComplete="off"
          autoFocus={autoFocus && index === 0}
          disabled={disabled}
          value={digit}
          onKeyDown={(event: React.KeyboardEvent<HTMLInputElement>) => handleKeyDown(event, index)}
          onChange={event => handleDigitChange(event.target.value, index)}
          id={`${index}`}
          onFocus={handleFocus}
          key={index}
          inputProps={{
            "data-testid": `${TestId.CodeInputDigit}${index}`,
            maxLength: index === digits.length - 1 ? 1 : digits.length,
            ref: digitReferences[index],
            style: { textAlign: "center" },
            pattern: "\\d*",
          }}
          className={className}
          type="text"
        />
      ))}
    </div>
  );
};

export default CodeInput;
