import browser from "browser-polyfill";
import {
  Button,
  CircularProgress,
  createStyles,
  makeStyles,
  TextField,
  ThemeProvider,
  Typography,
} from "@material-ui/core";
import { MessageKind, ReportFalseNegativeMessageResponse } from "common";
import React, { useEffect } from "react";
import { useCallback } from "react";
import { useState } from "react";
import ppTheme from "../pp-theme";

const useStyles = makeStyles(() =>
  createStyles({
    buttons: {
      alignItems: "center",
      display: "flex",
      flexDirection: "row",
      justifyContent: "space-between",
      width: "100%",
    },
    cancelButton: {
      "&:hover": {
        backgroundColor: "transparent",
      },
      backgroundColor: "transparent",
      border: "2px solid #FF5400",
      borderRadius: 0,
      color: "#FF5400",
      fontSize: 12,
      textTransform: "uppercase",
      width: 130,
    },
    errorText: {
      color: "#FF0000",
      fontSize: 12,
      fontWeight: 600,
      padding: "4px 0px",
    },
    progress: {
      color: "#FFFFFF",
    },
    sendButton: {
      "&:hover": {
        backgroundColor: "#FF5400",
      },
      backgroundColor: "#FF5400",
      border: "2px solid #FF5400",
      color: "#FFFFFF",
      fontSize: 12,
      textTransform: "uppercase",
      width: 130,
    },
    sentText: {
      textAlign: "center",
      fontSize: 16,
      lineHeight: "22px",
      color: "#FF5400",
      marginTop: 20,
      width: "100%",
    },
    title: {
      fontSize: 14,
      fontWeight: 600,
      marginBottom: 8,
      marginTop: 22,
      textTransform: "uppercase",
    },
  })
);

export enum ReportWebsiteState {
  NEW = "New",
  SENT = "Sent",
  SUCCESS = "Success",
  FAILED = "Failed",
}

export const reportWebsiteResultMap: Map<ReportWebsiteState, string> = new Map([
  [ReportWebsiteState.SUCCESS, "Thank you"],
  [ReportWebsiteState.FAILED, "There was an error, please try again later."],
]);

const SUCCESS_MESSAGE_TIMEOUT = 5000;

const ReportWebsitePopup = (): JSX.Element => {
  const classes = useStyles();
  const [comment, setComment] = useState<string>("");
  const [isCommentValid, setIsCommentValid] = useState<boolean>(true);
  const [reportWebsiteState, setReportWebsiteState] = useState<ReportWebsiteState>(
    ReportWebsiteState.NEW
  );

  useEffect(() => {
    if (
      reportWebsiteState === ReportWebsiteState.SUCCESS ||
      reportWebsiteState === ReportWebsiteState.FAILED
    ) {
      const timeoutId = setTimeout((): void => {
        window.close();
      }, SUCCESS_MESSAGE_TIMEOUT);

      return () => {
        clearTimeout(timeoutId);
      };
    }
  }, [reportWebsiteState]);

  const onCommentChange = (event: React.ChangeEvent<HTMLInputElement>): void => {
    setIsCommentValid(/^[\d\s(),.A-Za-z-]*$/.test(event.target.value));
    setComment(event.target.value);
  };

  const cancel = (): void => {
    window.close();
  };

  const send = useCallback((): void => {
    if (reportWebsiteState !== ReportWebsiteState.SENT && isCommentValid) {
      setReportWebsiteState(ReportWebsiteState.SENT);
      void browser.runtime
        .sendMessage({ kind: MessageKind.ReportFalseNegative, comment })
        .then((response: ReportFalseNegativeMessageResponse) => {
          setReportWebsiteState(
            response.success ?? false ? ReportWebsiteState.SUCCESS : ReportWebsiteState.FAILED
          );
        });
    }
  }, [comment, isCommentValid, reportWebsiteState, setReportWebsiteState]);

  return (
    <ThemeProvider theme={ppTheme}>
      {reportWebsiteState === ReportWebsiteState.SUCCESS ||
      reportWebsiteState === ReportWebsiteState.FAILED ? (
        <Typography component="div" className={classes.sentText}>
          {reportWebsiteResultMap.get(reportWebsiteState)}
        </Typography>
      ) : (
        <>
          <span className={classes.title}>Additional comment</span>
          <TextField
            multiline
            disabled={reportWebsiteState === ReportWebsiteState.SENT}
            rows={3}
            variant="outlined"
            onChange={onCommentChange}
            inputProps={{ maxLength: 150 }}
          />
          <div
            className={classes.errorText}
            style={{ visibility: isCommentValid ? "hidden" : "visible" }}
          >
            Invalid characters in comment.
          </div>
          <div className={classes.buttons}>
            <Button
              disabled={reportWebsiteState === ReportWebsiteState.SENT}
              className={classes.cancelButton}
              onClick={cancel}
            >
              Cancel
            </Button>
            <Button
              disabled={reportWebsiteState === ReportWebsiteState.SENT}
              className={classes.sendButton}
              onClick={send}
            >
              {reportWebsiteState === ReportWebsiteState.SENT ? (
                <CircularProgress size={16} className={classes.progress} />
              ) : (
                "Send"
              )}
            </Button>
          </div>
        </>
      )}
    </ThemeProvider>
  );
};

export default ReportWebsitePopup;
