import { logger } from "./frontend-logger";

export function registerGlobalErrorHandlers(): void {
  window.addEventListener("error", handleGlobalError);
  window.addEventListener("unhandledrejection", handleUnhandledRejection);
}

export function unregisterGlobalErrorHandlers(): void {
  window.removeEventListener("error", handleGlobalError);
  window.removeEventListener("unhandledrejection", handleUnhandledRejection);
}

const handleGlobalError = (event: ErrorEvent): void => {
  logger.error(`DOM-error-handler: Global DOM error caught: ${event.message}`, event);
};

const handleUnhandledRejection = (event: PromiseRejectionEvent): void => {
  let errorMessage = "Unhandled promise rejection: ";

  if (typeof event.reason === "string") {
    errorMessage += event.reason;
  } else if (event.reason instanceof Error) {
    errorMessage += event.reason.message || event.reason.stack;
  } else {
    errorMessage += JSON.stringify(event.reason);
  }

  logger.error(`DOM-error-handler: ${errorMessage}`, event);
};
