import browser from "browser-polyfill";
import React, { useCallback, useEffect, useState } from "react";

import { Button, CircularProgress, createStyles, makeStyles } from "@material-ui/core";
import { CognitoUser, CognitoUserSession } from "amazon-cognito-identity-js";
import { constant, times } from "lodash";
import CodeInput from "./code-input";
import { MessageKind, SigninIdentifyResponseKind, UserAuthorizationStatus } from "common";
import { TestId } from "../common/test-ids";
import { signinUrl } from "../common/url";
import { cognitoSigninIdentifyUser } from "../utils/cognito";

const useStyles = makeStyles(() =>
  createStyles({
    codeError: {
      color: "#CB3D3D",
      fontSize: 18,
      lineHeight: "18px",
      margin: "10px 0px",
    },
    codeInput: {
      height: 60,
      margin: "0px 6px",
      width: 60,
      fontFamily: "Circular Pro",
      "& .MuiInputBase-input": {
        color: "#1B1948",
        fontSize: 30,
        fontWeight: 450,
      },
      "& .MuiOutlinedInput-notchedOutline": {
        borderColor: "#000033",
        borderWidth: 1,
      },
      "&:hover": {
        "& .MuiOutlinedInput-notchedOutline": {
          borderColor: "#000033",
        },
      },
      "&.Mui-focused": {
        "& .MuiOutlinedInput-notchedOutline": {
          borderColor: "#000033",
        },
      },
    },
    content: {
      display: "flex",
      flexDirection: "column",
      alignItems: "center",
    },
    description: {
      lineHeight: "32px",
    },
    email: {
      color: "#1B1948",
      fontWeight: 700,
    },
    nextButton: {
      alignSelf: "center",
      fontWeight: 700,
      height: 50,
      letterSpacing: 1.56,
      marginTop: 30,
      width: "100%",
      "&:disabled": {
        color: "#FFFFFF",
        backgroundColor: "#FF5400",
        opacity: 0.5,
      },
    },
    progress: {
      color: "#FFFFFF",
    },
    resendSection: {
      fontSize: 16,
      marginTop: 30,
    },
    resendAction: {
      color: "#FF5400",
      fontWeight: 500,
      textDecoration: "none",
    },
    tipContent: {
      fontSize: 12,
      lineHeight: "18px",
      marginLeft: 8,
    },
    unlockTip: {
      color: "#616161",
      display: "flex",
      marginTop: 16,
      paddingBottom: 16,
    },
  })
);

const CODE_LENGTH = 6;
const RESEND_TIMEOUT = 30;
const SECOND_IN_MILLISECONDS = 1000;

const isPartial = (digits: string[]): boolean => digits.some(digit => !digit);

interface SigninVerifyUserProperties {
  email: string;
  cognitoUser: CognitoUser;
  handleSigninComplete: () => void;
  userPoolId: string;
  userPoolClientId: string;
}

export const SigninVerifyUser = ({
  cognitoUser,
  email,
  handleSigninComplete,
  userPoolClientId,
  userPoolId,
}: SigninVerifyUserProperties): JSX.Element => {
  const classes = useStyles();
  const [internalCognitoUser, setInternalCognitoUser] = useState(cognitoUser);
  const [codeDigits, setCodeDigits] = useState<string[]>(times(CODE_LENGTH, constant("")));
  const [resendTimer, setResendTimer] = useState(0);
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);
  const [firstCodeFill, setFirstCodeFill] = useState(true);

  useEffect(() => {
    const timerTick =
      resendTimer > 0
        ? setTimeout(() => setResendTimer(timer => timer - 1), SECOND_IN_MILLISECONDS)
        : undefined;
    return () => {
      if (timerTick) {
        clearTimeout(timerTick);
      }
    };
  }, [resendTimer]);

  const verifyUser = useCallback(
    (code: string): void => {
      internalCognitoUser.sendCustomChallengeAnswer(code, {
        onSuccess: (session: CognitoUserSession) => {
          void browser.runtime
            .sendMessage({
              kind: MessageKind.SaveAuthTokens,
              jwtToken: session.getIdToken().getJwtToken(),
              jwtTokenExpiration: session.getIdToken().getExpiration(),
              refreshToken: session.getRefreshToken().getToken(),
              lastUser: email,
              userPoolClientId,
              userPoolId,
            })
            .then(() => {
              handleSigninComplete();
            });
        },
        onFailure: () => {
          window.location.assign(
            `${signinUrl}&message=${UserAuthorizationStatus.VerificationSessionExpired}`
          );
        },
        customChallenge: () => {
          setError("This code isn't correct, try again");
          setLoading(false);
        },
      });
    },
    [email, internalCognitoUser, handleSigninComplete, userPoolClientId, userPoolId]
  );

  const handleVerify = useCallback((): void => {
    if (loading || isPartial(codeDigits)) {
      return;
    }
    setError("");
    setLoading(true);
    verifyUser(codeDigits.join(""));
  }, [loading, codeDigits, verifyUser]);

  useEffect(() => {
    if (!firstCodeFill) {
      return;
    }
    if (isPartial(codeDigits)) {
      return;
    }
    // finished filling code
    setFirstCodeFill(false);
    handleVerify();
  }, [codeDigits, firstCodeFill, handleVerify]);

  const handleResend = (): void => {
    if (resendTimer > 0) {
      return;
    }
    setResendTimer(RESEND_TIMEOUT);
    void cognitoSigninIdentifyUser(email).then(signinResult => {
      switch (signinResult.kind) {
        case SigninIdentifyResponseKind.Error: {
          console.error(
            "Failed to resend code, let user fill a new email address",
            signinResult.error
          );
          window.location.assign(signinUrl);
          return;
        }
        case SigninIdentifyResponseKind.CognitoUserPool: {
          setInternalCognitoUser(signinResult.cognitoUser);
          return;
        }
        case SigninIdentifyResponseKind.SigninUrl: {
          window.location.href = signinResult.signinUrl;
          return;
        }
      }
    });
  };

  return (
    <div className={classes.content}>
      <p className={classes.description} data-testid={TestId.SigninVerifyUserDescription}>
        We&apos;ve just sent a code to{" "}
        <span className={classes.email} data-testid={TestId.SigninVerifyUserEmail}>
          {email}
        </span>
        .<br />
        Enter the code here:
      </p>
      <CodeInput
        autoFocus
        className={classes.codeInput}
        disabled={loading}
        digits={codeDigits}
        onChange={setCodeDigits}
        onSubmit={handleVerify}
      />
      <div className={classes.codeError} data-testid={TestId.SigninVerifyUserError}>
        {error}
      </div>
      <Button
        className={classes.nextButton}
        color="primary"
        data-testid={TestId.SigninVerifyUserNextButton}
        variant="contained"
        onClick={handleVerify}
        disabled={isPartial(codeDigits)}
        disableElevation
      >
        {loading ? <CircularProgress size={14} className={classes.progress} /> : "CONTINUE"}
      </Button>
      <div className={classes.resendSection}>
        Didn&apos;t receive the code?{" "}
        {resendTimer === 0 ? (
          <a href="#" className={classes.resendAction} onClick={handleResend}>
            Resend code
          </a>
        ) : (
          <span data-testid={TestId.SigninVerifyUserResendDelay}>
            Resend code in {resendTimer} sec
          </span>
        )}
      </div>
    </div>
  );
};

export default SigninVerifyUser;
