import browser from "browser-polyfill";
import React, { useCallback } from "react";

import { MessageKind } from "common";
import { Button } from "@material-ui/core";
import { createStyles, makeStyles } from "@material-ui/styles";
import { ReactComponent as BannerIcon } from "../assets/banner-icon.svg";
import ppTheme from "../pp-theme";

import BaseToast, { SnackbarProperties } from "./base-toast";

const useStyles = makeStyles(() =>
  createStyles({
    closeButton: {
      alignSelf: "center",
      display: "flex",
      fontSize: 14,
      lineHeight: "18px",
      fontWeight: 450,
      backgroundColor: ppTheme.palette.secondary.main,
      borderRadius: 4,
      height: 50,
      color: "#FFFFFF",
      marginLeft: 24,
      width: 150,
      "&:hover": {
        backgroundColor: ppTheme.palette.secondary.main,
      },
    },
  })
);

const snackBarStyle: SnackbarProperties = {
  width: "100%",
  height: 133,
  top: 10,
  padding: "8px 6px",
};

interface BannerProperties {
  autoDismiss?: boolean;
  id: string;
  title: string;
  message: string;
}

const Banner: React.FC<BannerProperties> = ({
  autoDismiss = false,
  id,
  title,
  message,
}: BannerProperties) => {
  const classes = useStyles();

  const closeBanner = useCallback((): void => {
    void browser.runtime.sendMessage({
      kind: MessageKind.DismissNotificationToast,
      id,
    });
  }, [id]);

  const onCloseClick = (): void => {
    void closeBanner();
  };

  const buttonContent = (
    <Button className={classes.closeButton} onClick={onCloseClick}>
      Okay, got it
    </Button>
  );

  return (
    <BaseToast
      id={id}
      body={message}
      title={title}
      autoDismiss={autoDismiss}
      showCloseButton={false}
      horizontal="center"
      icon={<BannerIcon />}
      extraButtonComponents={buttonContent}
      snackBarStyle={snackBarStyle}
      updateHight={10}
      slideTransitionDirection="up"
    />
  );
};

export default Banner;
