import React, { useRef, useState, useEffect } from "react";
import Tooltip, { TooltipProps } from "@material-ui/core/Tooltip";
import Typography from "@material-ui/core/Typography";

type OverflowProperties = Omit<TooltipProps, "children">;

const OverflowTip = ({
  className,
  title,
  ...tooltipProperties
}: OverflowProperties): JSX.Element => {
  const [isOverflowed, setIsOverflow] = useState(false);
  const textElementReference = useRef<HTMLDivElement>(null);
  useEffect(() => {
    if (!textElementReference.current) {
      return;
    }
    setIsOverflow(
      textElementReference.current.scrollWidth > textElementReference.current.clientWidth
    );
  }, [textElementReference]);
  return (
    <Tooltip title={title} disableHoverListener={!isOverflowed} {...tooltipProperties}>
      <Typography className={className} component="div" noWrap ref={textElementReference}>
        {title}
      </Typography>
    </Tooltip>
  );
};

export default OverflowTip;
