import React from "react";
import { NotificationLevel, ToastSlideTransitionType } from "common";

import BaseToast, { levelToToastStyleProperties } from "./base-toast";

interface SensitiveSiteToastProperties {
  autoDismiss?: boolean;
  id: string;
  title: string;
  message: string;
  notificationLevel: NotificationLevel;
  slideTransitionDirection: ToastSlideTransitionType;
  debuggingData?: string;
}

const NotificationToast: React.FC<SensitiveSiteToastProperties> = ({
  autoDismiss = true,
  id,
  message,
  title,
  notificationLevel,
  slideTransitionDirection,
  debuggingData,
}: SensitiveSiteToastProperties) => {
  const header = levelToToastStyleProperties.get(notificationLevel)?.text;

  return (
    <BaseToast
      id={id}
      body={message}
      title={title}
      notificationLevel={notificationLevel}
      autoDismiss={autoDismiss}
      showCloseButton={true}
      headerText={debuggingData && header ? `${header} \u00B7 ${debuggingData}` : header}
      slideTransitionDirection={slideTransitionDirection}
    />
  );
};

export default NotificationToast;
