import React, { useEffect, useState } from "react";
import { createStyles, makeStyles } from "@material-ui/core";
import { ReactComponent as DownoladBlockLogo } from "../assets/download-block.svg";
import { getQueryParameters } from "../utils/url";
import { BlockPage, BlockType } from "./block-page";
import { CustomMaliciousDownloadMessage } from "../hooks/custome-message";
import useHmcConfiguration from "../hooks/use-hmc-configuration";
import { TestId } from "../common/test-ids";

const useStyles = makeStyles(() =>
  createStyles({
    fileName: {
      wordBreak: "break-all",
    },
  })
);

export const FileTypeBlockPage = (): JSX.Element => {
  const classes = useStyles();
  const configuration = useHmcConfiguration();
  const [fileName, setFileName] = useState<string>();
  const [message, setMessage] = useState<string>();

  useEffect(() => {
    const { fileName } = getQueryParameters();
    setFileName(fileName as string);
  }, [setFileName]);

  useEffect(() => {
    if (configuration) {
      setMessage(configuration.maliciousDownloadMessage);
    }
  }, [configuration]);

  return (
    <BlockPage
      icon={<DownoladBlockLogo />}
      title="You are not allowed to download this file"
      subtitle={
        message ? (
          <CustomMaliciousDownloadMessage message={message} filename={fileName} />
        ) : (
          <div data-testid={TestId.FileTypeBlockPageReason}>
            <span className={classes.fileName} data-testid={TestId.FileTypeBlockedFilename}>
              {fileName}
            </span>{" "}
            cannot be downloaded as the browser policy restricts downloading files of this type.
            Please contact your administrator for more information.
          </div>
        )
      }
      blockType={BlockType.File}
      testid={TestId.FileTypeBlockPage}
    />
  );
};
