import browser from "browser-polyfill";
import React, { useCallback, useEffect, useRef, useState } from "react";

import { IconButton, Slide, Snackbar, SnackbarContent } from "@material-ui/core";
import { createStyles, makeStyles } from "@material-ui/styles";
import CloseIcon from "@material-ui/icons/Close";
import { ReactComponent as InfoImage } from "../assets/toast-info.svg";
import { ReactComponent as WarningImage } from "../assets/toast-warning.svg";
import { ReactComponent as ErrorImage } from "../assets/toast-error.svg";
import { ReactComponent as SuccessImage } from "../assets/toast-success.svg";
import { ReactComponent as MaliciousImage } from "../assets/toast-mal.svg";
import { ReactComponent as PolicyImage } from "../assets/toast-policy.svg";
import { TestId } from "../common/test-ids";
import { MessageKind, NotificationLevel, ToastSlideTransitionType } from "common";
import ppTheme from "../pp-theme";

import { TransitionProps } from "@material-ui/core/transitions";
import useHmcConfiguration from "../hooks/use-hmc-configuration";

const useStyles = makeStyles(() =>
  createStyles({
    closeButton: {
      padding: 0,
    },
    content: {
      margin: "6px 12px",
      fontFamily: ppTheme.typography.fontFamily,
    },
    logo: {
      maxHeight: "36px !important",
      maxWidth: "118px !important",
    },
    logoRow: {
      color: ppTheme.palette.secondary.main,
      display: "flex",
      justifyContent: "space-between",
      alignItems: "center",
      fontSize: 11,
      padding: "0px 0px 3px 0px",
    },
    secondary: {
      color: "#616161",
    },
    snackbar: ({ width, height, top, padding }: SnackbarProperties) => ({
      "& .MuiSnackbarContent-root": {
        borderRadius: 8,
        padding: 0,
        width: "100%",
        height,
      },
      "& .MuiSnackbarContent-message": {
        width: "100%",
        padding,
      },
      top: top ?? 0,
      width: width ?? "unset",
    }),
    snackbarContent: ({ color }: SnackbarProperties) => ({
      background: "#FFFFFF",
      borderTop: "4px solid",
      borderColor: color,
    }),
    toastBody: {
      color: "#4F4D5C",
      fontSize: 12,
      whiteSpace: "pre-wrap",
      overflowWrap: "anywhere",
    },
    title: {
      color: ppTheme.palette.secondary.main,
      fontSize: 16,
      fontWeight: 450,
    },
    toastMessage: {
      display: "flex",
      flexDirection: "column",
    },
    toastTextContent: {
      display: "flex",
      flexDirection: "column",
      marginLeft: 4,
    },
    buttonAndTextContainer: {
      display: "flex",
      justifyContent: "space-between",
      padding: 1,
    },
    iconAndTextContainer: {
      display: "flex",
    },
    icon: {
      marginRight: 10,
      marginTop: 2,
    },
  })
);

export interface SnackbarProperties {
  color?: string;
  width?: string;
  height?: number;
  top?: number;
  padding?: string;
}

interface ToastStyleProperties {
  color: string;
  text: string;
  icon: JSX.Element;
}

export const levelToToastStyleProperties = new Map<NotificationLevel, ToastStyleProperties>([
  [
    NotificationLevel.Info,
    {
      color: ppTheme.notificationLevel.info,
      text: "Info",
      icon: <InfoImage key={NotificationLevel.Info} />,
    },
  ],
  [
    NotificationLevel.Warning,
    {
      color: ppTheme.notificationLevel.warning,
      text: "Warning",
      icon: <WarningImage key={NotificationLevel.Warning} />,
    },
  ],
  [
    NotificationLevel.Error,
    {
      color: ppTheme.notificationLevel.error,
      text: "Error",
      icon: <ErrorImage key={NotificationLevel.Error} />,
    },
  ],
  [
    NotificationLevel.Success,
    {
      color: ppTheme.notificationLevel.success,
      text: "Success",
      icon: <SuccessImage key={NotificationLevel.Success} />,
    },
  ],
  [
    NotificationLevel.Malicious,
    {
      color: ppTheme.notificationLevel.malicious,
      text: "Malicious",
      icon: <MaliciousImage key={NotificationLevel.Malicious} />,
    },
  ],
  [
    NotificationLevel.Policy,
    {
      color: ppTheme.notificationLevel.policy,
      text: "Organization Policy",
      icon: <PolicyImage key={NotificationLevel.Policy} />,
    },
  ],
]);

interface BaseToastProperties {
  id: string;
  title: string;
  autoDismiss: boolean;
  showCloseButton: boolean;
  body: string | JSX.Element;
  notificationLevel?: NotificationLevel;
  headerText?: string;
  icon?: JSX.Element;
  extraComponents?: JSX.Element;
  extraButtonComponents?: JSX.Element;
  horizontal?: "left" | "right" | "center";
  snackBarStyle?: SnackbarProperties;
  updateHight?: number;
  slideTransitionDirection?: ToastSlideTransitionType;
}

const initialCountdownSeconds = 8;
const msInSecond = 1_000;

const SlideTransitionDown = (properties: TransitionProps): JSX.Element => {
  return <Slide {...properties} direction="down" />;
};

const SlideTransitionUp = (properties: TransitionProps): JSX.Element => {
  return <Slide {...properties} direction="up" />;
};

const getIconComponent = (
  icon?: JSX.Element,
  notificationLevel?: NotificationLevel
): JSX.Element | undefined => {
  if (icon) {
    return icon;
  }
  return notificationLevel ? levelToToastStyleProperties.get(notificationLevel)?.icon : undefined;
};

const BaseToast: React.FC<BaseToastProperties> = ({
  id,
  title,
  autoDismiss,
  notificationLevel,
  showCloseButton,
  body,
  headerText,
  icon,
  extraComponents,
  extraButtonComponents,
  horizontal,
  snackBarStyle,
  updateHight,
  slideTransitionDirection,
}: BaseToastProperties) => {
  const toastReference = useRef<HTMLDivElement>();
  const color = notificationLevel
    ? levelToToastStyleProperties.get(notificationLevel)?.color
    : "transparent";
  const iconComponent = getIconComponent(icon, notificationLevel);
  const classes = useStyles({
    color,
    width: snackBarStyle?.width,
    height: snackBarStyle?.height,
    top: snackBarStyle?.top,
    padding: snackBarStyle?.padding,
  });
  const configuration = useHmcConfiguration();
  const [isOpen, setIsOpen] = useState(false);
  const [countdownSeconds, setCountdownSeconds] = useState(initialCountdownSeconds);
  const [isTimerActive, setIsTimerActive] = useState(false);
  const [logoRowOrganizationName, setLogoRowOrganizationName] = useState("Perception Point");

  useEffect(() => {
    setIsOpen(true);
    if (autoDismiss) {
      setIsTimerActive(true);
    }
  }, [autoDismiss]);

  useEffect(() => {
    if (configuration.customOrganizationName && configuration.customOrganizationName !== "") {
      setLogoRowOrganizationName(configuration.customOrganizationName);
    }
  }, [configuration]);

  useEffect(() => {
    if (isTimerActive) {
      const interval = setInterval(() => {
        setCountdownSeconds(value => value - 1);
      }, msInSecond);
      return () => clearInterval(interval);
    }
  }, [isTimerActive]);

  const closeToast = useCallback((): void => {
    setIsOpen(false);
    void browser.runtime.sendMessage({
      kind: MessageKind.DismissNotificationToast,
      id,
    });
  }, [id]);

  const onCloseClick = (): void => {
    void closeToast();
  };

  useEffect(() => {
    if (countdownSeconds === 0) {
      void closeToast();
      setIsTimerActive(false);
    }
  }, [countdownSeconds, closeToast]);

  useEffect(() => {
    if (!toastReference.current) {
      return;
    }

    if (toastReference.current.clientHeight === 0) {
      return;
    }

    const updateWindowSize = (height: number): void => {
      void browser.runtime.sendMessage({
        kind: MessageKind.ResizeToastWindow,
        height,
        id,
      });
    };

    void updateWindowSize(
      updateHight
        ? toastReference.current.clientHeight - updateHight
        : toastReference.current.clientHeight
    );
  }, [id, updateHight, toastReference?.current?.clientHeight]);

  const content = (
    <div className={classes.content}>
      <div className={classes.logoRow}>
        <div>
          <span data-test-id={TestId.ToastOrganizationName}>{logoRowOrganizationName}</span>
          {headerText && (
            <>
              {" \u00B7 "} <span data-test-id={TestId.ToastHeaderText}>{headerText}</span>
            </>
          )}
        </div>
        {showCloseButton && (
          <div>
            <IconButton size="medium" className={classes.closeButton} onClick={onCloseClick}>
              <CloseIcon fontSize="small" />
            </IconButton>
          </div>
        )}
      </div>
      <div className={classes.toastMessage}>
        <div className={classes.buttonAndTextContainer}>
          <div className={classes.iconAndTextContainer}>
            <div className={classes.icon}>{iconComponent}</div>
            <div className={classes.toastTextContent}>
              <div data-test-id={TestId.ToastTitle} className={classes.title}>
                {title}
              </div>
              <div className={classes.toastBody} data-test-id={TestId.ToastBody}>
                {body}
              </div>
            </div>
          </div>
          {extraButtonComponents}
        </div>
        {extraComponents}
      </div>
    </div>
  );

  const onMouseOver = useCallback(() => {
    setIsTimerActive(false);
  }, []);

  return (
    <Snackbar
      anchorOrigin={{
        horizontal: horizontal ? horizontal : "right",
        vertical: "top",
      }}
      open={isOpen}
      className={classes.snackbar}
      onMouseOver={onMouseOver}
      TransitionComponent={
        slideTransitionDirection === "up" ? SlideTransitionUp : SlideTransitionDown
      }
      ref={toastReference}
    >
      <SnackbarContent className={classes.snackbarContent} message={content} />
    </Snackbar>
  );
};

export default BaseToast;
