import browser from "browser-polyfill";
import React, { useEffect } from "react";
import { getQueryParameters } from "../utils/url";
import { getFile, MessageKind } from "common";

const blobType = "application/octet-stream";

// TODO: replace this function in NMS-5995
const logError = async (
  message: string,
  optionalParameters?: unknown[] | undefined
): Promise<void> => {
  await browser.runtime.sendMessage({
    kind: MessageKind.WriteLog,
    log: message,
    level: "error",
    optionalParameters,
  });
};

export const DownloadStoredFile = (): JSX.Element => {
  useEffect(() => {
    const { downloadProcessInfoId, downloadId, fileName } = getQueryParameters();

    let timeout: NodeJS.Timeout | false | undefined;

    async function getStoragedFile(): Promise<void> {
      let url = "";

      try {
        if (!fileName) {
          await logError("Download stored file: fileName is undefined.");
        }

        if (!downloadId) {
          await logError("Download stored file: downloadId is undefined.");
          window.close();
        }

        if (!downloadProcessInfoId) {
          await logError("Download stored file: downloadProcessInfoId is undefined.");
          window.close();
        }

        const blob = await getFile(downloadId as string);
        if (!blob) {
          throw new Error(`blob was not found ${downloadId as string}`);
        }

        const element = document.createElement("a");
        const file = new Blob([blob], {
          type: blobType,
        });
        url = URL.createObjectURL(file);
        const originalDownloadProcessInfoId = Array.isArray(downloadProcessInfoId)
          ? downloadProcessInfoId[0]
          : downloadProcessInfoId;
        element.href = `${url}#originDownloadProcessInfoId=${originalDownloadProcessInfoId}`;
        element.download = fileName as string;
        document.body.append(element);
        element.click();
      } catch (error) {
        await logError(
          "Download stored file:",
          error instanceof Error ? [error.message, error.stack] : [String(error)]
        );
      } finally {
        if (timeout !== false) {
          timeout = setTimeout(() => {
            if (url) {
              window.URL.revokeObjectURL(url);
            }
            window.close();
            window.name = "downloading file";
          }, 1000);
        }
      }
    }

    void getStoragedFile();

    return () => {
      if (timeout) {
        clearTimeout(timeout);
      }
      timeout = false;
    };
  }, []);

  return <div>Downloading File...</div>;
};
