import browser from "browser-polyfill";
import { useEffect, useState } from "react";
import { GetLastUserMessageResponse, MessageKind } from "common";

const useLastUser = (): string | undefined => {
  const [lastUser, setLastUser] = useState<string | undefined>();

  useEffect(() => {
    const refreshLastUser = (): void => {
      void browser.runtime
        .sendMessage({ kind: MessageKind.GetLastUser })
        .then(({ lastUser }: GetLastUserMessageResponse) => {
          setLastUser(lastUser);
        });
    };

    browser.storage.onChanged.addListener(refreshLastUser);
    refreshLastUser();

    return () => {
      browser.storage.onChanged.removeListener(refreshLastUser);
    };
  }, [setLastUser]);

  return lastUser;
};

export default useLastUser;
