import browser from "browser-polyfill";
import { useEffect, useState } from "react";
import { CustomHtmlPages, MessageKind, PageType } from "common";

const defaultPages: CustomHtmlPages = Object.fromEntries(
  Object.values(PageType).map(value => [value, ""])
);

const useCustomHtmlPage = (page: PageType): string | undefined => {
  const [htmlPages, setHtmlPages] = useState<CustomHtmlPages>({ ...defaultPages });
  useEffect(() => {
    const refreshHtml = (): void => {
      void browser.runtime
        .sendMessage({ kind: MessageKind.GetConfiguration })
        .then(() => browser.storage.local.get({ customHtmlPages: {} }))
        .then(result => {
          setHtmlPages(result.customHtmlPages);
        });
    };

    browser.storage.onChanged.addListener(refreshHtml);
    refreshHtml();

    return () => {
      browser.storage.onChanged.removeListener(refreshHtml);
    };
  }, [setHtmlPages]);

  return htmlPages[page];
};

export default useCustomHtmlPage;
