import browser from "browser-polyfill";
import { createStyles, makeStyles, Typography } from "@material-ui/core";
import { GetTabProtectionDataMessageResponse, MessageKind } from "common";
import React, { useEffect, useState } from "react";
import { ReactComponent as ShieldImage } from "../assets/shield.svg";

const useStyles = makeStyles(() =>
  createStyles({
    content: {
      display: "flex",
      flexDirection: "column",
      fontSize: 14,
      alignItems: "center",
      width: 330,
      margin: "0 auto",
    },
    contentRow: {
      alignItems: "center",
      display: "flex",
      flexDirection: "row",
      justifyContent: "space-between",
      width: "100%",
      margin: "8px 10px",
    },
    contentTitle: {
      textAlign: "center",
      fontSize: 24,
      lineHeight: "38px",
      fontWeight: 900,
      color: "#FF5400",
      margin: "12px 0px",
      letterSpacing: -1.5,
    },
    downloadLogsButton: {
      backgroundColor: "transparent",
      padding: 0,
      minHeight: 20,
      minWidth: 20,
      "&:hover": {
        backgroundColor: "transparent",
      },
    },
    fieldName: {
      whiteSpace: "nowrap",
      marginRight: 10,
    },
    lastUserTooltip: {
      top: "20px !important",
    },
    overflow: {
      fontSize: 14,
    },
  })
);

const shortenUUID = (uuid: string): string => uuid.slice(0, 8);

const TabProtectionPopup = (): JSX.Element => {
  const classes = useStyles();
  const [tabId, setTabId] = useState<number>();
  const [navigationSetId, setNavigationSetId] = useState<string>("");
  const [sessionId, setSessionId] = useState<string>("");
  const [sessionResolvingOrigin, setSessionResolvingOrigin] = useState<string>("");
  const [emailScanId, setEmailScanId] = useState<string>("");
  const [activationStatus, setActivationStatus] = useState<string>("");
  const [detectionActive, setDetectionActive] = useState<boolean>(false);

  useEffect(() => {
    void browser.runtime
      .sendMessage({
        kind: MessageKind.GetTabProtectionData,
      })
      .then(
        ({
          tabId,
          navigationSetId,
          sessionId,
          sessionResolvingOrigin,
          emailScanId,
          activationStatus,
          detectionActive,
        }: GetTabProtectionDataMessageResponse) => {
          setTabId(tabId);
          setNavigationSetId(shortenUUID(navigationSetId ?? ""));
          setSessionId(shortenUUID(sessionId ?? ""));
          setSessionResolvingOrigin(sessionResolvingOrigin ?? "");
          setEmailScanId(shortenUUID(emailScanId ?? ""));
          setActivationStatus(activationStatus ?? "");
          setDetectionActive(Boolean(detectionActive));
        }
      );
  }, []);

  return (
    <div className={classes.content}>
      <ShieldImage />
      <Typography component="div" className={classes.contentTitle}>
        Tab Protection Data
      </Typography>
      <div className={classes.contentRow}>
        <div>Activation Status:</div>
        <div>{activationStatus}</div>
      </div>
      <div className={classes.contentRow}>
        <div>Tab ID:</div>
        <div>{tabId}</div>
      </div>
      <div className={classes.contentRow}>
        <div>Navigation Set ID:</div>
        <div>{navigationSetId}</div>
      </div>
      <div className={classes.contentRow}>
        <div>Session ID:</div>
        <div>{sessionId}</div>
      </div>
      <div className={classes.contentRow}>
        <div>Session resolving origin:</div>
        <div>{sessionResolvingOrigin}</div>
      </div>
      <div className={classes.contentRow}>
        <div>Detection:</div>
        <div>{detectionActive ? "Active" : "Inactive"}</div>
      </div>
      <div className={classes.contentRow}>
        <div>Email scan ID:</div>
        <div>{emailScanId}</div>
      </div>
    </div>
  );
};

export default TabProtectionPopup;
