import React, { ReactNode, useCallback } from "react";
import { ErrorBoundary, FallbackProps } from "react-error-boundary";
import { logger } from "./frontend-logger";

interface AppErrorTrapProperties {
  children: ReactNode;
}

const AppErrorTrap: React.FC<AppErrorTrapProperties> = ({ children }) => {
  // Define the logErrorToService function using useCallback to avoid re-creating it on every render
  const logErrorToService = useCallback(
    (error: Error, errorInfo: { componentStack: string }): void => {
      logger.error("React error:", error, errorInfo);
    },
    []
  );

  // Define the renderFallback function using useCallback to avoid re-creating it on every render
  const renderFallback = useCallback((_properties: FallbackProps): JSX.Element => {
    // render nothing for now
    return <></>;
  }, []);

  return (
    <ErrorBoundary FallbackComponent={renderFallback} onError={logErrorToService}>
      {children}
    </ErrorBoundary>
  );
};

export default AppErrorTrap;
