import { createStyles, makeStyles } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { ReactComponent as BlockedExtensionLogo } from "../assets/blocked-extension-icon.svg";
import { BlockPage, BlockPageColor, BlockType } from "./block-page";
import { getQueryParameters } from "../utils/url";
import { TestId } from "../common/test-ids";

const useStyles = makeStyles(() =>
  createStyles({
    fileName: {
      wordBreak: "break-all",
    },
    subtitle: {
      fontWeight: 600,
      marginBottom: 20,
    },
  })
);

const getSubtitleWithRiskLevel = (riskLevel: string, isSuccessfulDisabling: boolean): string => {
  const subtitleSuffix = isSuccessfulDisabling
    ? " The extension has been disabled."
    : " Please uninstall the extension.";
  return `The organization's browser extension policies don't allow usage of extensions with ${riskLevel?.toLowerCase()} risk scores.${subtitleSuffix}`;
};

export const DisabledExtensionDueToRiskScoreBlockPage = (): JSX.Element => {
  const classes = useStyles();
  const [extensionDisplayName, setExtensionDisplayName] = useState<string>();
  const [riskLevel, setRiskLevel] = useState<string>();
  const [isSuccessfulDisabling, setIsSuccessfulDisabling] = useState<boolean>();

  useEffect(() => {
    const { extensionDisplayName, riskLevel, isSuccessfulDisabling } = getQueryParameters();
    setExtensionDisplayName(extensionDisplayName as string);
    setRiskLevel(riskLevel as string);
    setIsSuccessfulDisabling(isSuccessfulDisabling === "true");
  }, []);

  const aftermath = isSuccessfulDisabling ? "was disabled" : "is not premitted";

  const title = riskLevel
    ? `The "${
        extensionDisplayName as string
      }" extension has a ${riskLevel?.toLowerCase()} risk score.`
    : `"${extensionDisplayName as string}" extension ${aftermath}`;

  const subtitleWithRiskLevel = getSubtitleWithRiskLevel(
    riskLevel as string,
    Boolean(isSuccessfulDisabling)
  );

  const subtitleWithoutRiskLevel = isSuccessfulDisabling ? (
    <>
      {"Usage of the "}
      <span
        className={classes.fileName}
        data-testid={TestId.DisabledExtensionDueToRiskScoreDisplayName}
      >
        {`"${extensionDisplayName as string}"`}
      </span>
      {
        " extension is not allowed by your administrator due to the company's browser extension policies."
      }
    </>
  ) : (
    <>
      {"Usage of the "}
      <span
        className={classes.fileName}
        data-testid={TestId.DisabledExtensionDueToRiskScoreDisplayName}
      >
        {`"${extensionDisplayName as string}"`}
      </span>
      {
        " extension is not permitted by the company's browser extension policies. Please uninstall the extension."
      }
    </>
  );

  const subtitle = riskLevel ? subtitleWithRiskLevel : subtitleWithoutRiskLevel;

  return (
    <BlockPage
      icon={<BlockedExtensionLogo />}
      title={title}
      color={BlockPageColor.Gray}
      subtitle={
        <div
          className={classes.subtitle}
          data-testid={TestId.DisabledExtensionDueToRiskScoreBlockPageSubtitle}
        >
          {subtitle}
        </div>
      }
      blockType={BlockType.Extension}
      testid={TestId.DisabledExtensionDueToRiskScoreBlockPage}
    />
  );
};
