import React from "react";

import { Button } from "@material-ui/core";
import createStyles from "@material-ui/core/styles/createStyles";
import makeStyles from "@material-ui/core/styles/makeStyles";

import { ReactComponent as ArrowBack } from "../assets/arrow.svg";
import { ReactComponent as PpTextLogoImage } from "../assets/pp-text-logo.svg";
import { TestId } from "../common/test-ids";
import { signinUrl } from "../common/url";

const useStyles = makeStyles(() =>
  createStyles({
    back: {
      backgroundColor: "transparent",
      marginTop: 20,
      width: "fit-content",
      "&:hover": {
        backgroundColor: "transparent",
      },
    },
    description: {
      display: "flex",
      flexDirection: "column",
      fontSize: 16,
      height: "100%",
      margin: "auto 80px",
      width: "40%",
    },
    descriptionWrapper: {
      margin: "auto 0px",
      maxWidth: 500,
    },
    divider: {
      borderTop: "2px solid #FF5400",
      display: "inline-flex",
      marginBottom: 60,
      marginTop: 20,
      width: 120,
    },
    title: {
      color: "#1B1948",
      fontSize: 44,
      fontWeight: 900,
      lineHeight: "46px",
      marginBottom: 20,
      marginTop: 60,
    },
    subtitle: {
      color: "#4F4D5C",
      fontSize: 18,
      fontWeight: 500,
      marginBottom: 15,
    },
  })
);

interface SigninWelcomeMessageProperties {
  showBackButton?: boolean;
}

export const SigninWelcomeMessage = ({
  showBackButton = false,
}: SigninWelcomeMessageProperties): JSX.Element => {
  const classes = useStyles();

  const handleBack = (): void => {
    window.location.assign(signinUrl);
  };

  return (
    <div className={classes.description}>
      {showBackButton && (
        <Button onClick={handleBack} className={classes.back}>
          <ArrowBack />
        </Button>
      )}
      <div className={classes.descriptionWrapper}>
        <PpTextLogoImage />
        <div className={classes.title} data-testid={TestId.WelcomeHeader}>
          Welcome to a safer browsing experience.
        </div>
        <div className={classes.subtitle} data-testid={TestId.WelcomeSubHeader}>
          The Perception Point browser extension protects you and your organization against web
          threats, including phishing attempts and malicious downloads.
        </div>
        <hr className={classes.divider} />
        <div style={{ color: "#4F4D5C" }} data-testid={TestId.WelcomeDescription}>
          The extension runs in the background without interfering with your work and without
          storing any personal data
        </div>
      </div>
    </div>
  );
};

export default SigninWelcomeMessage;
