import React, { ReactNode, useEffect, useState } from "react";
import { createStyles, makeStyles } from "@material-ui/core";
import useHmcConfiguration from "../hooks/use-hmc-configuration";
import { CustomLink, getRelevantCustomLinkConfigurationBasedOnBlockType } from "./custom-link";
import { BlockType } from "../pages/block-page";
import { TestId } from "../common/test-ids";

const useStyles = makeStyles(() =>
  createStyles({
    subtitle: {
      fontSize: 18,
    },
  })
);

export interface BlockPageDescriptionButton {
  label: string;
  onClick?: () => void;
  inverted?: boolean;
}

export interface BlockPageDescriptionProperties {
  subtitle: JSX.Element | string;
  displayCustomLink: boolean;
  children?: ReactNode;
  blockType: BlockType;
}

export const BlockPageDescription = ({
  children,
  subtitle,
  blockType,
  displayCustomLink,
}: BlockPageDescriptionProperties): JSX.Element => {
  const configuration = useHmcConfiguration();
  const [customLink, setCustomLink] = useState<string | undefined>();
  const classes = useStyles();

  useEffect(() => {
    if (configuration) {
      const customLink = getRelevantCustomLinkConfigurationBasedOnBlockType(
        configuration,
        blockType
      );
      if (customLink) {
        setCustomLink(customLink);
      }
    }
  }, [configuration, blockType]);

  return (
    <>
      <div className={classes.subtitle} dir="auto" data-testid={TestId.BlockPageSubtitle}>
        {subtitle}
      </div>
      {children}
      {customLink && displayCustomLink && (
        <CustomLink blockType={blockType} customLink={customLink} />
      )}
    </>
  );
};
