import { ThemeProvider } from "@material-ui/core";
import React from "react";
import ReactDOM from "react-dom";
import { NotificationLevel, ScanProgressStatus, PageType, ToastSlideTransitionType } from "common";
import queryString from "querystring";

import theme from "./theme";
import "./index.scss";
import { LocalStorageProvider } from "./components/local-storage-provider";
import {
  WebsiteBlock,
  FileWarning,
  FileTypeBlockPage,
  MaliciousBlock,
  PolicyBlock,
  PhishingBlock,
  BlockUnscannableFile,
  DisabledExtensionBlockPage,
  DisabledExtensionDueToRiskScoreBlockPage,
  EncryptedFilePasswordPrompt,
  CustomHtmlPage,
  Signin,
  SigninRedirectingPage,
  DownloadLocalLogs,
  DownloadStoredFile,
  ManagedWebsiteBlock,
} from "./pages";
import { ScanProgressToast, Banner, NotificationToast } from "./toasts";
import StandalonePopup from "./popups/standalone-popup";
import { registerGlobalErrorHandlers } from "./common/DOM-error-handler";
import AppErrorTrap from "./common/AppErrorTrap";
import DownloadManager from "./pages/download-manager-page";

const queryParams = queryString.parse((window.location.search || "").replace(/^\?/, ""));

const notificationToastPage = (): JSX.Element => {
  const {
    autoDismiss,
    body,
    title,
    notificationLevel,
    id,
    slideTransitionDirection,
    debuggingData,
  } = queryParams;
  if (
    [autoDismiss, body, title, notificationLevel, id, slideTransitionDirection, debuggingData].some(
      item => Array.isArray(item)
    )
  ) {
    return <></>;
  }
  return (
    <NotificationToast
      id={id as string}
      message={body as string}
      title={title as string}
      notificationLevel={notificationLevel as NotificationLevel}
      {...(autoDismiss && { autoDismiss: autoDismiss === "1" })}
      slideTransitionDirection={slideTransitionDirection as ToastSlideTransitionType}
      {...(debuggingData && { debuggingData: debuggingData as string })}
    />
  );
};

const scanProgressToastPage = (): JSX.Element => {
  const {
    autoDismiss,
    fileName,
    maxScanProgressPercentage,
    minScanProgressPercentage,
    scanProgressStagePercentage,
    downloadedBytes,
    changeInterval,
    id,
    scanStatus,
    fileSizeBytes,
    downloadId,
    notificationLevel,
    showCancelButton,
    slideTransitionDirection,
    shouldShowDownloadProgressInToast,
    debuggingData,
  } = queryParams;
  if (
    [
      autoDismiss,
      fileName,
      maxScanProgressPercentage,
      minScanProgressPercentage,
      scanProgressStagePercentage,
      downloadedBytes,
      changeInterval,
      id,
      scanStatus,
      fileSizeBytes,
      downloadId,
      notificationLevel,
      showCancelButton,
      slideTransitionDirection,
      shouldShowDownloadProgressInToast,
      debuggingData,
    ].some(item => Array.isArray(item))
  ) {
    return <></>;
  }
  return (
    <ScanProgressToast
      {...(autoDismiss && { autoDismiss: autoDismiss === "1" })}
      id={id as string}
      {...(maxScanProgressPercentage && {
        maxScanProgressPercentage: Number.parseInt(maxScanProgressPercentage as string, 10),
      })}
      {...(minScanProgressPercentage && {
        minScanProgressPercentage: Number.parseInt(minScanProgressPercentage as string, 10),
      })}
      {...(scanProgressStagePercentage && {
        scanProgressStagePercentage: Number.parseInt(scanProgressStagePercentage as string, 10),
      })}
      {...(downloadedBytes && {
        downloadedBytes: Number.parseInt(downloadedBytes as string, 10),
      })}
      changeInterval={Number.parseInt(changeInterval as string, 10)}
      fileSizeBytes={+fileSizeBytes}
      fileName={fileName as string}
      scanStatus={scanStatus as ScanProgressStatus}
      downloadId={+downloadId}
      notificationLevel={notificationLevel as NotificationLevel}
      {...(showCancelButton && { showCancelButton: showCancelButton === "1" })}
      slideTransitionDirection={slideTransitionDirection as ToastSlideTransitionType}
      shouldShowDownloadProgressInToast={shouldShowDownloadProgressInToast === "1"}
      {...(debuggingData && { debuggingData: debuggingData as string })}
    />
  );
};

const BannerNotification = (): JSX.Element => {
  const { autoDismiss, body, title, id } = queryParams;
  if ([autoDismiss, body, title, id].some(item => Array.isArray(item))) {
    return <></>;
  }
  return (
    <Banner
      id={id as string}
      message={body as string}
      title={title as string}
      {...(autoDismiss && { autoDismiss: autoDismiss === "1" })}
    />
  );
};

const encryptedFilePasswordPage = (): JSX.Element => {
  const { downloadId, filename } = queryParams;
  if ([downloadId, filename].some(item => Array.isArray(item))) {
    return <></>;
  }
  return (
    <EncryptedFilePasswordPrompt
      downloadId={Number.parseInt(downloadId as string, 10)}
      filename={filename as string}
    />
  );
};

// eslint-disable-next-line complexity
const getPage = (pageType: PageType): JSX.Element => {
  switch (pageType) {
    case PageType.SignIn:
      return <Signin />;
    case PageType.SignInRedirectingPage:
      return <SigninRedirectingPage />;
    case PageType.WebsiteBlock:
      return <WebsiteBlock />;
    case PageType.WebsiteMaliciousBlock:
      return <MaliciousBlock />;
    case PageType.Block:
      return <ManagedWebsiteBlock />;
    case PageType.PolicyBlock:
      return <PolicyBlock />;
    case PageType.PhishingBlock:
      return <PhishingBlock />;
    case PageType.FileWarning:
      return <FileWarning />;
    case PageType.FileTypeBlock:
      return <FileTypeBlockPage />;
    case PageType.DownloadLocalLogs:
      return <DownloadLocalLogs />;
    case PageType.DownloadStoredFile:
      return <DownloadStoredFile />;
    case PageType.Notification:
      return notificationToastPage();
    case PageType.ScanProgress:
      return scanProgressToastPage();
    case PageType.UnscannableFileBlockPage:
      return <BlockUnscannableFile />;
    case PageType.DisabledExtensionBlockPage:
      return <DisabledExtensionBlockPage />;
    case PageType.DisabledExtensionDueToRiskScoreBlockPage:
      return <DisabledExtensionDueToRiskScoreBlockPage />;
    case PageType.PasswordInputPage:
      return encryptedFilePasswordPage();
    case PageType.Banner:
      return BannerNotification();
    case PageType.Popup:
      return <StandalonePopup />;
    case PageType.DownloadManagerPage:
      return <DownloadManager />;
    default:
      return <></>;
  }
};

const renderPage = (pageType: PageType): JSX.Element => {
  const { custom } = queryParams;
  if (!Array.isArray(custom) && custom === "true") {
    return <CustomHtmlPage pageType={pageType} originalPage={getPage(pageType)} />;
  }

  return getPage(pageType);
};

registerGlobalErrorHandlers();

const App = (): JSX.Element => {
  // unComment to enable authentication flow
  // useEffect(() => {
  //   chrome.runtime.sendMessage({
  //     kind: MessageKind.GetToken,
  //   }, ({ jwtToken }: GetTokenResponse) => {
  //     if (!jwtToken && queryParams.page !== "signin" && queryParams.page !== "verifyUser") {
  //       window.location.assign(signinUrl);
  //     }
  //   });
  // }, []);

  return !Array.isArray(queryParams.page) ? renderPage(queryParams.page as PageType) : <></>;
};

ReactDOM.render(
  <AppErrorTrap>
    <ThemeProvider theme={theme}>
      <LocalStorageProvider>
        <App />
      </LocalStorageProvider>
    </ThemeProvider>
  </AppErrorTrap>,
  document.querySelector("#root")
);
