import React from "react";
import { createStyles, Link, makeStyles } from "@material-ui/core";
import { useContinueAnyway } from "../hooks/block-page-hooks";
import { TestId } from "../common/test-ids";

interface StyleParameters {
  color: string;
}

const useStyles = makeStyles(() =>
  createStyles({
    details: {
      fontSize: 18,
      fontWeight: 200,
    },
    link: {
      color: ({ color }: StyleParameters) => color,
      cursor: "pointer",
      textDecoration: "underline",
    },
  })
);

export interface BlockPageDetailsProperties {
  isDetailsTab: boolean;
  details?: JSX.Element | string;
  scanId?: string;
  setIsReportAsSafePage: React.Dispatch<React.SetStateAction<boolean>>;
  displayReportingWebsite: boolean;
  displayContinueAnyway: boolean;
  styleParameters: StyleParameters;
}

export const BlockPageDetails = ({
  isDetailsTab,
  setIsReportAsSafePage,
  details,
  scanId,
  displayReportingWebsite,
  displayContinueAnyway,
  styleParameters,
}: BlockPageDetailsProperties): JSX.Element => {
  const classes = useStyles(styleParameters);

  const continueAnyway = useContinueAnyway();

  return (
    <div
      className={classes.details}
      style={{ visibility: isDetailsTab ? "visible" : "hidden" }}
      dir="auto"
      data-testid={TestId.BlockPageDetails}
    >
      {details}
      {displayReportingWebsite && scanId && (
        <span data-testid={TestId.ReportDetectionIssueSpan}>
          If you believe that this website should not be blocked, please{" "}
          <Link
            className={classes.link}
            onClick={() => setIsReportAsSafePage(true)}
            data-testid={TestId.ReportDetectionIssueLink}
          >
            report a detection issue.
          </Link>{" "}
        </span>
      )}
      {displayContinueAnyway && (
        <span data-testid={TestId.ContinueAnywaySpan}>
          If you understand the risks to your security, you can still{" "}
          <Link
            className={classes.link}
            onClick={continueAnyway}
            data-testid={TestId.ContinueAnywayLink}
          >
            visit this unsafe site.
          </Link>
        </span>
      )}
    </div>
  );
};
