import browser from "browser-polyfill";
import { Button, createStyles, makeStyles, Typography } from "@material-ui/core";
import { GetExtensionSettingsMessageResponse, GetLocalLogsResponse, MessageKind } from "common";
import React, { useEffect, useState } from "react";
import { ReactComponent as SettingsImage } from "../assets/settings.svg";
import { ReactComponent as DownloadLogsImage } from "../assets/download-button.svg";
import { ReactComponent as FactoryResetImage } from "../assets/reset-button.svg";
import { TestId } from "../common/test-ids";
import useHmcConfiguration from "../hooks/use-hmc-configuration";
import OverflowTip from "../components/overflow-tip";
import { useLocalStorage } from "../hooks/use-local-storage";

const useStyles = makeStyles(() =>
  createStyles({
    content: {
      display: "flex",
      flexDirection: "column",
      fontSize: 14,
      alignItems: "center",
      width: 330,
      margin: "0 auto",
    },
    contentRow: {
      alignItems: "center",
      display: "flex",
      flexDirection: "row",
      justifyContent: "space-between",
      width: "100%",
      margin: "8px 10px",
    },
    contentTitle: {
      textAlign: "center",
      fontSize: 24,
      lineHeight: "38px",
      fontWeight: 900,
      color: "#FF5400",
      margin: "12px 0px",
      letterSpacing: -1.5,
    },
    downloadLogsButton: {
      backgroundColor: "transparent",
      padding: 0,
      minHeight: 20,
      minWidth: 20,
      "&:hover": {
        backgroundColor: "transparent",
      },
    },
    fieldName: {
      whiteSpace: "nowrap",
      marginRight: 10,
    },
    lastUserTooltip: {
      top: "20px !important",
    },
    overflow: {
      fontSize: 14,
    },
  })
);

export interface SettingsPopupProperties {
  lastUser: string | undefined;
}

const SettingsPopup = ({ lastUser }: SettingsPopupProperties): JSX.Element => {
  const classes = useStyles();
  const configuration = useHmcConfiguration();
  const [enableDownloadLocalLogsBeforeLogin, setEnableDownloadLocalLogsBeforeLogin] =
    useState(false);
  const [enableDownloadLocalLogs, setEnableDownloadLocalLogs] = useState(false);
  const [showLogsButton, setShowLogsButton] = useState(false);
  const [enableFactoryResetBeforeLogin, setEnableFactoryResetBeforeLogin] = useState(false);
  const [enableFactoryReset, setEnableFactoryReset] = useState(false);
  const [showResetButton, setShowResetButton] = useState(false);
  const [logFileName, setLogFileName] = useState<string>("");
  const [version, setVersion] = useState<string>("");
  const lastModified = useLocalStorage("latestConfigurationTimestamp", "");

  useEffect(() => {
    void browser.runtime
      .sendMessage({
        kind: MessageKind.GetExtensionSettings,
      })
      .then(({ extensionSettings }: GetExtensionSettingsMessageResponse) => {
        setEnableDownloadLocalLogsBeforeLogin(
          extensionSettings?.enableDownloadLocalLogsBeforeLogin ?? false
        );
        setEnableFactoryResetBeforeLogin(extensionSettings?.enableFactoryResetBeforeLogin ?? false);
      });
    setVersion(browser.runtime.getManifest().version);
  }, []);

  useEffect(() => {
    if (configuration) {
      setEnableDownloadLocalLogs(configuration.enableDownloadLocalLogs ?? false);
      setEnableFactoryReset(configuration.enableFactoryReset ?? false);
    }
  }, [configuration, setEnableDownloadLocalLogs, setEnableFactoryReset]);

  useEffect(() => {
    setShowLogsButton(lastUser ? enableDownloadLocalLogs : enableDownloadLocalLogsBeforeLogin);
    setShowResetButton(lastUser ? enableFactoryReset : enableFactoryResetBeforeLogin);
  }, [
    lastUser,
    enableDownloadLocalLogs,
    enableDownloadLocalLogsBeforeLogin,
    enableFactoryReset,
    enableFactoryResetBeforeLogin,
  ]);

  const onDownloadLogsClick = (): void => {
    void browser.runtime
      .sendMessage({
        kind: MessageKind.GetLocalLogs,
      })
      .then(({ logFileName }: GetLocalLogsResponse) => {
        if (logFileName) {
          setLogFileName(logFileName);
        }
      });
  };

  const onFactoryResetClick = (): void => {
    void browser.runtime.sendMessage({
      kind: MessageKind.FactoryReset,
    });
  };

  return (
    <div className={classes.content}>
      <SettingsImage />
      <Typography component="div" className={classes.contentTitle}>
        Advanced Browser Security
      </Typography>
      <div className={classes.contentRow}>
        <div>Version:</div>
        <div data-testid={TestId.VersionValueExtensionPopup}>{version}</div>
      </div>
      {lastUser && (
        <div className={classes.contentRow}>
          <div className={classes.fieldName}>Signed in as:</div>
          <OverflowTip
            className={classes.overflow}
            title={lastUser}
            placement="top"
            classes={{
              popper: classes.lastUserTooltip,
            }}
            data-testid={TestId.SignedInUserExtensionPopup}
          />
        </div>
      )}
      <div className={classes.contentRow}>
        <div className={classes.fieldName}>Last modified:</div>
        <OverflowTip
          className={classes.overflow}
          title={lastModified}
          placement="top"
          classes={{
            popper: classes.lastUserTooltip,
          }}
          data-testid={TestId.LastModifiedExtensionPopup}
        />
      </div>
      {showLogsButton && (
        <div className={classes.contentRow}>
          <div>Download logs:</div>
          {logFileName && <div>{logFileName}</div>}
          <Button
            className={classes.downloadLogsButton}
            onClick={onDownloadLogsClick}
            data-testid={TestId.DownloadsLogsButton}
          >
            <DownloadLogsImage />
          </Button>
        </div>
      )}
      {showResetButton && (
        <div className={classes.contentRow}>
          <div>Factory reset:</div>
          <Button
            className={classes.downloadLogsButton}
            onClick={onFactoryResetClick}
            data-testid={TestId.FacotryResetButton}
          >
            <FactoryResetImage />
          </Button>
        </div>
      )}
    </div>
  );
};

export default SettingsPopup;
