import React, { useEffect } from "react";
import { getAsFileContent } from "common";

export const DownloadLocalLogs = (): JSX.Element => {
  useEffect(() => {
    let timeout: NodeJS.Timeout | false | undefined;
    async function getLogs(): Promise<void> {
      const logs = await getAsFileContent();
      const element = document.createElement("a");
      const file = new Blob([logs], {
        type: "text/plain",
      });
      const url = URL.createObjectURL(file);
      element.href = url;
      element.download = `logs_${new Date().toISOString()}.txt`;
      document.body.append(element);
      element.click();
      if (timeout === false) {
        return;
      }
      timeout = setTimeout(() => {
        window.URL.revokeObjectURL(url);
        window.close();
      }, 1000);
    }
    void getLogs();
    return () => {
      if (timeout) {
        clearTimeout(timeout);
      }
      timeout = false;
    };
  }, []);

  return <div>Downloading logs...</div>;
};
