import useHmcConfiguration from "./use-hmc-configuration";
import { ReactComponent as PpTextLogoImage } from "../assets/pp-text-logo.svg";
import React from "react";

export interface PpTextLogoProperties {
  className: string;
}

export const PpTextLogo = ({ className }: PpTextLogoProperties): JSX.Element => {
  const { hidePPLogo, customLogoUrl, customOrganizationName } = useHmcConfiguration();

  const ppLogo =
    customOrganizationName === undefined || customOrganizationName === "" ? (
      <PpTextLogoImage className={className} />
    ) : (
      <></>
    );

  const image: JSX.Element = customLogoUrl ? (
    <img src={customLogoUrl} style={{ maxHeight: 100, maxWidth: 200 }} className={className} />
  ) : (
    ppLogo
  );

  return <>{hidePPLogo !== undefined && !hidePPLogo && image}</>;
};
