import React, { useEffect, useState } from "react";
import { createStyles, Link, makeStyles } from "@material-ui/core";

import { TestId } from "../common/test-ids";
import useHmcConfiguration from "../hooks/use-hmc-configuration";
import { BlockType } from "../pages/block-page";
import { Configuration } from "common";

export function getRelevantCustomLinkConfigurationBasedOnBlockType(
  configuration: Partial<Configuration>,
  blockType: BlockType
): string | undefined {
  switch (blockType) {
    case BlockType.File:
      if (
        configuration.maliciousDownloadCustomMessageLink &&
        configuration.maliciousDownloadCustomMessageLink !== ""
      ) {
        return configuration.maliciousDownloadCustomMessageLink;
      }
      break;
    case BlockType.MaliciousWebsite:
      if (
        configuration.maliciousWebsiteCustomMessageLink &&
        configuration.maliciousWebsiteCustomMessageLink !== ""
      ) {
        return configuration.maliciousWebsiteCustomMessageLink;
      }
      break;
    case BlockType.WebsiteRule:
      return configuration.policyRestrictedWebsiteCustomMessage?.customMessageLink;
  }
  return;
}

const useStyles = makeStyles(() =>
  createStyles({
    customLinkDiv: {
      fontSize: 18,
      marginTop: 20,
    },
    customLink: {
      color: "#FFFFFF",
      cursor: "pointer",
      textDecoration: "underline",
    },
  })
);

export interface CustomLinkProperties {
  blockType: BlockType;
  customLink: string;
}

export const CustomLink = ({ blockType, customLink }: CustomLinkProperties): JSX.Element => {
  const classes = useStyles();
  const configuration = useHmcConfiguration();
  const [customLinkTitle, setCustomLinkTitle] = useState<string | undefined>();

  useEffect(() => {
    if (configuration) {
      switch (blockType) {
        case BlockType.File:
          if (
            configuration.maliciousDownloadCustomMessageLinkTitle &&
            configuration.maliciousDownloadCustomMessageLinkTitle !== ""
          ) {
            setCustomLinkTitle(configuration.maliciousDownloadCustomMessageLinkTitle);
          }
          break;
        case BlockType.MaliciousWebsite:
          if (
            configuration.maliciousWebsiteCustomMessageLinkTitle &&
            configuration.maliciousWebsiteCustomMessageLinkTitle !== ""
          ) {
            setCustomLinkTitle(configuration.maliciousWebsiteCustomMessageLinkTitle);
          }
          break;
        case BlockType.WebsiteRule:
          setCustomLinkTitle(
            configuration.policyRestrictedWebsiteCustomMessage?.customMessageLinkTitle
          );
      }
    }
  }, [configuration, blockType]);

  return (
    <div className={classes.customLinkDiv}>
      <Link
        className={classes.customLink}
        href={customLink}
        target="_blank"
        data-testid={TestId.CustomMaliciousLink}
      >
        {customLinkTitle ?? customLink}
      </Link>
    </div>
  );
};
