import { createStyles, makeStyles } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { ReactComponent as WebsiteBlockLogo } from "../assets/website-block.svg";
import { BlockPage, BlockType } from "./block-page";
import useHmcConfiguration from "../hooks/use-hmc-configuration";
import { getUrl } from "../utils/url";
import { TestId } from "../common/test-ids";

const useStyles = makeStyles(() =>
  createStyles({
    domain: {
      wordBreak: "break-all",
    },
    subtitle: {
      fontWeight: 600,
      marginBottom: 20,
    },
  })
);

export const PolicyBlock = (): JSX.Element => {
  const classes = useStyles();
  const configuration = useHmcConfiguration();
  const [url, setUrl] = useState<string>();
  const [message, setMessage] = useState<string>();

  useEffect(() => {
    setUrl(getUrl());
  }, [setUrl]);

  useEffect(() => {
    if (configuration) {
      setMessage(configuration.maliciousWebsiteMessage);
    }
  }, [configuration]);

  return (
    <BlockPage
      allowBackToSafety
      allowReportingWebsite
      icon={<WebsiteBlockLogo />}
      title="Access to this site is blocked"
      subtitle={
        message || (
          <>
            <div className={classes.subtitle} data-testid={TestId.PolicyBlockPageSubtitle}>
              <span className={classes.domain} data-testid={TestId.PolicyBlockedUrlOrText}>
                {url ? url : "This site"}
              </span>{" "}
              was blocked by the Administrator
            </div>
            <div data-testid={TestId.PolicyBlockReason}>
              Due to the company&apos;s browsing policies,{" "}
              <span className={classes.domain} data-testid={TestId.PolicyBlockedUrl}>
                {url}
              </span>{" "}
              is currently blocked.
            </div>
          </>
        )
      }
      blockType={BlockType.MaliciousWebsite}
      testid={TestId.PolicyBlockPage}
    />
  );
};
