import React, { ReactNode } from "react";
import { createStyles, makeStyles } from "@material-ui/core";
import { TestId } from "../common/test-ids";
import { PpTextLogo as Logo } from "../hooks/pp-text-logo";
import { ReactComponent as BrandLinesDecoration } from "../assets/block-page-background.svg";

export interface BlockPageContainerProperties {
  children: ReactNode;
  styleParameters: StyleParameters;
  testid: TestId;
}

interface StyleParameters {
  primary: string;
  secondary: string;
}

const useStyles = makeStyles(() =>
  createStyles({
    outerContainer: {
      alignItems: "center",
      backgroundColor: ({ primary }: StyleParameters) => primary,
      display: "flex",
      flexDirection: "row",
      fontFamily: "Circular Pro, Arial",
      height: "100vh",
    },
    decorations: {
      height: "70%",
      marginTop: "auto",
    },
    sideContent: {
      alignItems: "start",
      backgroundColor: ({ secondary }: StyleParameters) => secondary,
      display: "flex",
      height: "inherit",
      flexDirection: "column",
      justifyContent: "space-between",
      padding: "0px 60px",
      width: "20%",
    },
    logo: {
      marginTop: 40,
    },
    innerContainer: {
      color: ({ secondary }: StyleParameters) => secondary,
      display: "flex",
      flexDirection: "column",
      fontSize: 20,
      justifyContent: "center",
      height: "inherit",
      overflowX: "hidden",
      maxWidth: 700,
      margin: "0% 7.83%",
    },
  })
);

export const BlockPageContainer = ({
  children,
  styleParameters,
  testid,
}: BlockPageContainerProperties): JSX.Element => {
  const classes = useStyles(styleParameters);

  return (
    <div className={classes.outerContainer} data-testid={testid}>
      <div className={classes.sideContent}>
        <Logo className={classes.logo} />
        <BrandLinesDecoration className={classes.decorations} />
      </div>
      <div className={classes.innerContainer}>{children}</div>
    </div>
  );
};
