import browser from "browser-polyfill";
import {
  AuthenticationDetails,
  CognitoUser,
  CognitoUserPool,
  ICognitoUserData,
  MessageKind,
  SigninIdentifyUserResponse,
  SigninIdentifyError,
  SigninIdentifyResponseKind,
} from "common";

interface SigninIdentifyUserCognitoUserResult {
  kind: SigninIdentifyResponseKind.CognitoUserPool;
  cognitoUser: CognitoUser;
  userPoolId: string;
  userPoolClientId: string;
}

interface SigninIdentifyUserSigninUrlResult {
  kind: SigninIdentifyResponseKind.SigninUrl;
  signinUrl: string;
}

interface SigninIdentifyUserFailureResult {
  kind: SigninIdentifyResponseKind.Error;
  error: SigninIdentifyError;
}

export async function cognitoSigninIdentifyUser(
  email: string
): Promise<
  | SigninIdentifyUserCognitoUserResult
  | SigninIdentifyUserSigninUrlResult
  | SigninIdentifyUserFailureResult
> {
  const results = (await browser.runtime.sendMessage({
    kind: MessageKind.SigninIdentifyUser,
    email,
  })) as SigninIdentifyUserResponse;
  switch (results.kind) {
    case SigninIdentifyResponseKind.Error: {
      return {
        kind: SigninIdentifyResponseKind.Error,
        error: results.error,
      };
    }
    case SigninIdentifyResponseKind.SigninUrl: {
      return {
        kind: SigninIdentifyResponseKind.SigninUrl,
        signinUrl: results.signinUrl,
      };
    }
    case SigninIdentifyResponseKind.CognitoUserPool: {
      const cognitoUserPool = new CognitoUserPool(results.cognitoUserPoolData);

      const cognitoUserConfig: ICognitoUserData = {
        Username: email,
        Pool: cognitoUserPool,
      };

      const cognitoUser = new CognitoUser(cognitoUserConfig);

      const authenticationDetails = new AuthenticationDetails({
        Username: email,
      });
      cognitoUser.setAuthenticationFlowType("CUSTOM_AUTH");

      return await new Promise(resolve => {
        cognitoUser.initiateAuth(authenticationDetails, {
          // on custom auth flow the on success will never be called but must be provided
          onSuccess: () => {
            console.info("Cognito Success");
          },
          onFailure: _error => {
            resolve({
              kind: SigninIdentifyResponseKind.Error,
              error: SigninIdentifyError.NotFound,
            });
          },
          customChallenge: (_challengeParameters: unknown) => {
            resolve({
              kind: SigninIdentifyResponseKind.CognitoUserPool,
              cognitoUser,
              userPoolClientId: results.cognitoUserPoolData.ClientId,
              userPoolId: results.cognitoUserPoolData.UserPoolId,
            });
          },
        });
      });
    }
  }
}
