import React, { useEffect, useState } from "react";

import createStyles from "@material-ui/core/styles/createStyles";
import makeStyles from "@material-ui/core/styles/makeStyles";
import { Card, LinearProgress } from "@material-ui/core";
import filesize from "filesize";
import { DownloadProcessInfo, DownloadProcessState, Verdict } from "common";
import ppTheme from "../pp-theme";

const useStyles = makeStyles(() =>
  createStyles({
    card: {
      border: "1px solid #E5E5E5",
      borderRadius: "6px",
      display: "flex",
      flexDirection: "column",
      gap: "10px",
      marginBottom: 20,
      padding: 30,
      width: 800,
    },
    lineProgress: {
      "&.MuiLinearProgress-colorPrimary:not(.MuiLinearProgress-buffer)": {
        backgroundColor: "#EEEEEE",
      },
      "& .MuiLinearProgress-colorPrimary": {
        backgroundColor: "#EEEEEE",
      },
      "& .MuiLinearProgress-barColorPrimary": {
        backgroundColor: ppTheme.notificationLevel.info,
      },
    },
    linearProgressContainer: {
      marginTop: 10,
      width: "100%",
    },
    urlSpan: {
      fontWeight: 150,
      marginTop: 8,
    },
  })
);

function getPrettifiedFileSize(fileSizeBytes: number): string {
  return filesize(fileSizeBytes, {
    base: 2,
    standard: "jedec",
  });
}

function getInlineModeText(isInline?: boolean): string {
  return isInline ? "Inline" : "Parallel";
}

function getVerdictText(verdict?: Verdict): string {
  switch (verdict) {
    case Verdict.Clean:
      return "Clean";
    case Verdict.Malware:
      return "Malware";
    case Verdict.Suspicious:
      return "Suspicious";
    case Verdict.Unknown:
    case undefined:
      return "Unknown";
    default:
      return "Unknown";
  }
}

function getScanStateText(scanState: DownloadProcessState): string {
  switch (scanState) {
    case DownloadProcessState.New:
      return "New";
    case DownloadProcessState.Started:
      return "Started";
    case DownloadProcessState.UrlsPreScanFinished:
      return "UrlsPreScanFinished";
    case DownloadProcessState.PartialDownloadAnalyzed:
      return "PartialDownloadAnalyzed";
    case DownloadProcessState.UserDownloadSettled:
      return "UserDownloadSettled";
    case DownloadProcessState.Downloaded:
      return "Downloaded";
    case DownloadProcessState.SignaturePreScanFinished:
      return "SignaturePreScanFinished";
    case DownloadProcessState.Uploaded:
      return "Uploaded";
    case DownloadProcessState.Scanning:
      return "Scanning";
    case DownloadProcessState.Completed:
      return "Completed";
    case DownloadProcessState.Failed:
      return "Failed";
    case DownloadProcessState.VerdictChanged:
      return "VerdictChanged";
    default:
      return "Unknown";
  }
}

const finalScanStatuses = new Set([
  DownloadProcessState.Completed,
  DownloadProcessState.Failed,
  DownloadProcessState.VerdictChanged,
]);

function getScanStateData(state: DownloadProcessState, verdict?: Verdict): string {
  return finalScanStatuses.has(state) ? getVerdictText(verdict) : getScanStateText(state);
}

type activeScanStates = Exclude<
  DownloadProcessState,
  DownloadProcessState.Completed | DownloadProcessState.Failed | DownloadProcessState.VerdictChanged
>;

interface ProgressPercentages {
  min: number;
  max: number;
}

const activeScanStatesToPercentages: Record<activeScanStates, ProgressPercentages> = {
  [DownloadProcessState.New]: { min: 0, max: 1 },
  [DownloadProcessState.Enriched]: { min: 1, max: 3 },
  [DownloadProcessState.Started]: { min: 3, max: 5 },
  [DownloadProcessState.UrlsPreScanFinished]: { min: 5, max: 10 },
  [DownloadProcessState.PartialDownloadAnalyzed]: { min: 10, max: 12 },
  [DownloadProcessState.UserDownloadSettled]: { min: 12, max: 15 },
  [DownloadProcessState.Downloaded]: { min: 15, max: 30 },
  [DownloadProcessState.SignaturePreScanFinished]: { min: 30, max: 35 },
  // TODO (ABS-1910): set max to 45
  [DownloadProcessState.Uploaded]: { min: 35, max: 100 },
  // TODO (ABS-1910): set min to 45
  [DownloadProcessState.Scanning]: { min: 100, max: 100 },
};

function getUpdatedScanProgress(scanState: DownloadProcessState): ProgressPercentages {
  return activeScanStatesToPercentages[scanState as activeScanStates];
}

interface DownloadCardProperties {
  downloadProcessInfo: DownloadProcessInfo;
}

export const DownloadDataCard: React.FC<DownloadCardProperties> = ({
  downloadProcessInfo,
}: DownloadCardProperties) => {
  const [scanState, setScanState] = useState<DownloadProcessState>(downloadProcessInfo.state);
  const [shouldUpdateScanProgress, setShouldUpdateScanProgress] = useState<boolean>(
    !finalScanStatuses.has(downloadProcessInfo.state)
  );
  const initialScanProgressPercentages = getUpdatedScanProgress(downloadProcessInfo.state);
  const [updatedScanProgress, setUpdatedScanProgress] = useState(
    initialScanProgressPercentages?.min ?? 100
  );
  const [updatedMaxScanProgress, setUpdatedMaxScanProgress] = useState(
    initialScanProgressPercentages?.max ?? 100
  );

  const classes = useStyles();

  useEffect(() => {
    if (downloadProcessInfo.state !== scanState) {
      setScanState(downloadProcessInfo.state);
    }

    let timeout: NodeJS.Timeout | false | undefined;
    function dismissProgressBar(): void {
      if (timeout === false) {
        return;
      }
      timeout = setTimeout(() => {
        setShouldUpdateScanProgress(false);
      }, 1000);
    }

    if (finalScanStatuses.has(scanState)) {
      setUpdatedScanProgress(100);
      void dismissProgressBar();
      return () => {
        if (timeout) {
          clearTimeout(timeout);
        }
        timeout = false;
      };
    } else {
      const scanProgressPercentages = getUpdatedScanProgress(scanState);
      setUpdatedScanProgress(scanProgressPercentages.min);
      setUpdatedMaxScanProgress(scanProgressPercentages.max);
    }
  }, [
    setShouldUpdateScanProgress,
    downloadProcessInfo.state,
    scanState,
    setScanState,
    setUpdatedScanProgress,
    setUpdatedMaxScanProgress,
  ]);

  useEffect(() => {
    const scanStateMinValue = getUpdatedScanProgress(scanState)?.min;
    let interval: NodeJS.Timeout | undefined = setInterval(() => {
      if (!interval) {
        return;
      }

      if (!shouldUpdateScanProgress) {
        clearInterval(interval);
        interval = undefined;
        return;
      }

      if (
        (updatedMaxScanProgress !== scanStateMinValue &&
          updatedScanProgress === updatedMaxScanProgress - 1) ||
        updatedScanProgress > updatedMaxScanProgress - 1
      ) {
        clearInterval(interval);
        interval = undefined;
        return;
      }
      setUpdatedScanProgress(updatedScanProgress + 1);
    }, 2000);
    return () => {
      if (!interval) {
        return;
      }
      clearInterval(interval);
      interval = undefined;
    };
  }, [
    setUpdatedScanProgress,
    updatedScanProgress,
    updatedMaxScanProgress,
    scanState,
    shouldUpdateScanProgress,
  ]);

  return (
    <Card className={classes.card}>
      <span>
        <b>{downloadProcessInfo.download.filename}</b>
      </span>
      <span className={classes.urlSpan}>{downloadProcessInfo.download.urls.url}</span>
      <span>
        {getPrettifiedFileSize(downloadProcessInfo.download.fileSize)}
        {"  \u00B7  "}
        {getInlineModeText(downloadProcessInfo?.configuration?.isInline)}
        {"  \u00B7  "}
        {getScanStateData(downloadProcessInfo.state, downloadProcessInfo?.verdict?.verdict)}
      </span>
      {shouldUpdateScanProgress && (
        <div className={classes.linearProgressContainer}>
          <LinearProgress
            variant="determinate"
            className={classes.lineProgress}
            value={updatedScanProgress}
          />
        </div>
      )}
    </Card>
  );
};

export default DownloadDataCard;
