import React from "react";
import { createStyles, makeStyles } from "@material-ui/core";

import { TestId } from "../common/test-ids";

const useStyles = makeStyles(() =>
  createStyles({
    subtitle: {
      fontSize: 18,
    },
    long: {
      wordBreak: "break-all",
    },
  })
);

interface CustomMaliciousMessageProperties {
  categories?: string[];
  maliciousMessage: string;
  placeholder?: string;
  replacementValue?: string;
}

export interface CustomMaliciousWebsiteMessageProperties {
  message: string;
  categories?: string[];
  url?: string;
}

export interface CustomMaliciousWebsiteMessageProperties {
  message: string;
  categories?: string[];
  filename?: string;
}

const CustomMaliciousMessage = ({
  categories = [],
  maliciousMessage,
  placeholder,
  replacementValue,
}: CustomMaliciousMessageProperties): JSX.Element => {
  const classes = useStyles();

  const message = maliciousMessage.replace("<categories>", categories.join(", "));

  return (
    <div className={classes.subtitle} dir="auto" data-testid={TestId.CustomMaliciousMessage}>
      {placeholder &&
        message.split(placeholder).flatMap((value, index) => {
          return index === 0
            ? value
            : [
                <span key={index} className={classes.long}>
                  {replacementValue}
                </span>,
                value,
              ];
        })}
    </div>
  );
};

export const CustomMaliciousWebsiteMessage = ({
  message,
  categories = [],
  url,
}: CustomMaliciousWebsiteMessageProperties): JSX.Element => {
  return (
    <CustomMaliciousMessage
      maliciousMessage={message}
      replacementValue={url}
      placeholder="<URL>"
      categories={categories}
    />
  );
};

export const CustomMaliciousDownloadMessage = ({
  message,
  categories = [],
  filename,
}: CustomMaliciousWebsiteMessageProperties): JSX.Element => {
  return (
    <CustomMaliciousMessage
      maliciousMessage={message}
      replacementValue={filename}
      placeholder="<filename>"
      categories={categories}
    />
  );
};
