import browser from "browser-polyfill";
import { useCallback, useEffect, useState } from "react";
import { Configuration } from "common";

const useHmcConfiguration = (): Partial<Configuration> => {
  const [configuration, setConfiguration] = useState<Partial<Configuration>>({});

  const refreshConfiguration = useCallback(
    (changes: Record<string, browser.Storage.StorageChange>): void => {
      const { latestConfiguration } = changes;
      if (latestConfiguration && latestConfiguration.newValue) {
        setConfiguration(latestConfiguration.newValue);
      }
    },
    [setConfiguration]
  );

  useEffect(() => {
    browser.storage.onChanged.addListener(refreshConfiguration);
    void browser.storage.local.get({ latestConfiguration: {} }).then(result => {
      setConfiguration(result.latestConfiguration);
    });

    return () => {
      browser.storage.onChanged.removeListener(refreshConfiguration);
    };
  }, [refreshConfiguration]);

  return configuration;
};

export default useHmcConfiguration;
