import { createStyles, makeStyles } from "@material-ui/core";
import { BlockPageDetectionMode } from "common";
import React, { useEffect, useState } from "react";
import { ReactComponent as WebsiteBlockLogo } from "../assets/website-block.svg";
import { ReactComponent as WebsiteWarnLogo } from "../assets/warn-page-icon.svg";
import { BlockPage, BlockType, ContinueAnywayType } from "./block-page";
import { CustomMaliciousWebsiteMessage } from "../hooks/custome-message";
import useHmcConfiguration from "../hooks/use-hmc-configuration";
import { getQueryParameters } from "../utils/url";
import { TestId } from "../common/test-ids";

const useStyles = makeStyles(() =>
  createStyles({
    domain: {
      wordBreak: "break-all",
    },
  })
);

interface ContentByWebsiteBlockAction {
  icon: JSX.Element;
  title: string;
  subtitle: JSX.Element;
  detailsText: string;
  continueAnyway?: ContinueAnywayType;
}

export const ManagedWebsiteBlock = (): JSX.Element => {
  const classes = useStyles();
  const configuration = useHmcConfiguration();
  const [domain, setDomain] = useState<string>();
  const [categories, setCategories] = useState<string[]>();
  const [message, setMessage] = useState<string>();
  const [customMessage, setCustomMessage] = useState<string>();
  const [blockPageDetectionMode, setBlockPageDetectionMode] = useState<BlockPageDetectionMode>(
    BlockPageDetectionMode.Block
  );

  useEffect(() => {
    const { dom, categories, blockPageDetectionMode, customMessage } = getQueryParameters();
    setDomain(dom as string);
    setCategories(categories ? (categories as string).split(",") : []);
    setBlockPageDetectionMode(
      (blockPageDetectionMode as BlockPageDetectionMode) ?? BlockPageDetectionMode.Block
    );
    if (customMessage) {
      setCustomMessage(customMessage as string);
    }
  }, [setDomain, setCategories, setBlockPageDetectionMode, setCustomMessage]);

  useEffect(() => {
    if (configuration) {
      setMessage(configuration.policyRestrictedWebsiteCustomMessage?.customMessage);
    }
  }, [configuration]);

  const ContentByDetectionMode: Record<BlockPageDetectionMode, ContentByWebsiteBlockAction> = {
    [BlockPageDetectionMode.Warn]: {
      icon: <WebsiteWarnLogo />,
      title: "Proceed with caution",
      subtitle: (
        <>
          Keep in mind the organization&apos;s security guidelines when accessing the following
          website: &nbsp;
          <span className={classes.domain} data-testid={TestId.ManagedBlockedDomain}>
            {domain}
          </span>
        </>
      ),
      detailsText: "This website is included in the following categories:",
      continueAnyway: ContinueAnywayType.Button,
    },
    [BlockPageDetectionMode.Block]: {
      icon: <WebsiteBlockLogo />,
      title: "This website is blocked",
      subtitle: (
        <>
          Access to &quot;
          <span className={classes.domain} data-testid={TestId.ManagedBlockedDomain}>
            {domain}
          </span>
          &quot; is blocked by your administrator due to the company&apos;s browsing policies.
        </>
      ),
      detailsText:
        "Access to this website is blocked because it is included in the following categories:",
    },
  };

  return (
    <BlockPage
      icon={ContentByDetectionMode[blockPageDetectionMode].icon}
      title={ContentByDetectionMode[blockPageDetectionMode].title}
      subtitle={
        customMessage || message ? (
          <CustomMaliciousWebsiteMessage
            message={(customMessage ?? message) as string}
            url={domain}
            categories={categories}
          />
        ) : (
          ContentByDetectionMode[blockPageDetectionMode].subtitle
        )
      }
      blockType={BlockType.WebsiteRule}
      details={
        categories && categories.length > 0 ? (
          <>
            {ContentByDetectionMode[blockPageDetectionMode].detailsText}&nbsp;
            <ul data-testid={TestId.ManagedBlockedCategories}>
              {categories.map(category => (
                <li key={category}>{category}</li>
              ))}
            </ul>
          </>
        ) : (
          ""
        )
      }
      detectionMode={blockPageDetectionMode}
      continueAnyway={ContentByDetectionMode[blockPageDetectionMode].continueAnyway}
      testid={TestId.ManagedBlockPage}
    />
  );
};
