import { createStyles, makeStyles } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { ReactComponent as WebsiteBlockLogo } from "../assets/website-block.svg";
import { BlockPage, BlockPageColor, BlockType, ContinueAnywayType } from "./block-page";
import useHmcConfiguration from "../hooks/use-hmc-configuration";
import { getQueryParameters, getUrl } from "../utils/url";
import { TestId } from "../common/test-ids";
import { BlockPageDetectionMode } from "common";

const useStyles = makeStyles(() =>
  createStyles({
    continueAnywayLink: {
      color: "#FFFFFF",
      cursor: "pointer",
      textDecoration: "underline",
    },
    domain: {
      wordBreak: "break-all",
    },
    subtitle: {
      fontWeight: 600,
      marginBottom: 20,
    },
  })
);

export const MaliciousBlock = (): JSX.Element => {
  const classes = useStyles();
  const configuration = useHmcConfiguration();
  const [url, setUrl] = useState<string>();
  const [message, setMessage] = useState<string>();
  const [detectionMode, setDetectionMode] = useState<BlockPageDetectionMode>(
    BlockPageDetectionMode.Block
  );

  useEffect(() => {
    setUrl(getUrl());
    const { blockPageDetectionMode } = getQueryParameters();
    setDetectionMode(blockPageDetectionMode as BlockPageDetectionMode);
  }, [setUrl, setDetectionMode]);

  useEffect(() => {
    if (configuration) {
      setMessage(configuration.maliciousWebsiteMessage);
    }
  }, [configuration]);

  return (
    <BlockPage
      allowBackToSafety
      continueAnyway={ContinueAnywayType.Link}
      detectionMode={detectionMode}
      allowReportingWebsite
      icon={<WebsiteBlockLogo />}
      title="Dangerous site ahead"
      color={BlockPageColor.Red}
      subtitle={
        message || (
          <>
            <div className={classes.subtitle} data-testid={TestId.MaliciousBlockPageSubtitle}>
              <span className={classes.domain} data-testid={TestId.MaliciousBlockUrlOrText}>
                {url ? url : "This site"}
              </span>
              &nbsp;was blocked
            </div>
            <div data-testid={TestId.MaliciousBlockReason}>
              Our system detected that the{" "}
              <span className={classes.domain} data-testid={TestId.MaliciousBlockUrl}>
                {url}
              </span>{" "}
              website may try to steal data, harm your device, or negatively affect your browsing
              experience.
            </div>
          </>
        )
      }
      blockType={BlockType.MaliciousWebsite}
      testid={TestId.MaliciousBlockPage}
    />
  );
};
